import { isNullOrUndefined } from 'util';
import { Injectable } from '@angular/core';
import { CheckNullOrUndefinedOrEmpty } from '../utils/common-function';


@Injectable(
  {
    providedIn: "root",
  }
)
export class ConvertService {

  constructor() { }

    downloadFile(data, filename='data' ,  totalPrice, typeReportValue , productTotalPrice) {
      let csvData;

      if(!CheckNullOrUndefinedOrEmpty(typeReportValue)){
        if (typeReportValue === 'total_sales') {
          csvData = this.ConvertToCSV(data, ['orderID','customerName', 'phoneNumber', 'storeID', 'storeName', 'productName', 'product_price', 'quantity', 'order_price',
           'discount', 'orderDay'], ['Order ID','Customer Name', 'Customer Phone Number', 'Store ID', 'Store Name', 'Product',
          'Product Price', 'Quantity', 'Order Price' ,'Discount', 'Order Day'] ,totalPrice , productTotalPrice);

        }
      }

        let blob = new Blob(['\ufeff' + csvData], { type: 'text/csv;charset=utf-8;' });
        let dwldLink = document.createElement("a");
        let url = URL.createObjectURL(blob);
        let isSafariBrowser = navigator.userAgent.indexOf('Safari') != -1 && navigator.userAgent.indexOf('Chrome') == -1;
        if (isSafariBrowser) {  //if Safari open in new window to save file with random filename.
            dwldLink.setAttribute("target", "_blank");
        }
        dwldLink.setAttribute("href", url);
        dwldLink.setAttribute("download", filename + ".csv");
        dwldLink.style.visibility = "hidden";
        document.body.appendChild(dwldLink);
        dwldLink.click();
        document.body.removeChild(dwldLink);
    }

    ConvertToCSV(objArray, headerList , option,totalPrice , productTotalPrice) {
         let array = typeof objArray != 'object' ? JSON.parse(objArray) : objArray;
         let str = '';
         let row = 'No,';
         let total = 'Total'
         for (let index in option) {
             row += option[index] + ',';
         }
         row = row.slice(0, -1);
         str += row + '\r\n';
         for (let i = 0; i < array.length; i++) {
             let line = (i+1)+'';
             for (let index in headerList) {
                let head = headerList[index];

                 line += ',' + array[i][head];
             }
             str += line + '\r\n';
         }
         let line1 = ',' + ',' + ',' + ',' + ',' + ',' + total + ',' + productTotalPrice + ',' +',' + totalPrice;
         str += line1 + '\r\n';
         return str;
     }

}

export class Report {
  orderID: string;
  customerName: string;
  phoneNumber: string;
  orderDay: string;
  storeID: number;
  storeName: string;
  product_price: number;
  order_price: number;
  discount: string;
  productName: string;
  quantity: number;
}

