import { Component, OnInit } from '@angular/core';
import { Title } from 'app/core/enum/title';
import { BkPointCategoryService } from 'app/core/service/category-bkpoint.service';

@Component({
  selector: 'app-menu',
  templateUrl: './menu.component.html',
  styleUrls: ['./menu.component.scss']
})
export class MenuComponent implements OnInit {

  constructor(
    private bkPointCategoryService : BkPointCategoryService
  ) { }
  title = Title.DOT;
  productsInit = [];
  ngOnInit(): void {
    this.search("")
  }

  search(value: string){
    setTimeout(() => {
      this.bkPointCategoryService.searchCategorys(value).subscribe( data => {
        // console.log(data)
        this.productsInit = data
      })
    }, 500);
  }


}
