import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { Mode } from 'app/core/enum/mode';
import { Title } from 'app/core/enum/title';
import { BranchService } from 'app/core/service/branch.service';
import { BkPointCategoryService } from 'app/core/service/category-bkpoint.service';
import { environment } from 'environments/environment';
import { Location } from '@angular/common'
import { CheckNullOrUndefinedOrEmpty } from 'app/core/utils/common-function';
import { CommonDialogComponent } from 'app/main/common-dialog/common-dialog.component';
import { BkPointProductService } from 'app/core/service/product-bkpoint.service';
import { ProductForm } from 'app/core/models/bk-point/product-bkpoint.model';

@Component({
  selector: 'app-create-product',
  templateUrl: './create-product.component.html',
  styleUrls: ['./create-product.component.scss']
})
export class CreateProductComponent implements OnInit {

  constructor(
    private location : Location,
    public dialog: MatDialog,
    private branchService : BranchService,
    private bkPointCategoryService : BkPointCategoryService,
    private bkPointProductService: BkPointProductService,
    private activatedRoute : ActivatedRoute,
    private router: Router,
  ) { }

  title = Title.LEFT;

  storageUrl = environment.storageUrl;

  maximumImageSize : number = environment.imageSize;
  /** image url */
  myProfileImgUrl: string;
  
  coverphotokey : string;
  buttonName: string = "CREATE";
  submmited: boolean = false;

  uuid: string;
  mode: string;
  activeOnlineStore: boolean;
  name: string;
  description: string;
  selectedCategory;
  listCategory;
  price: number;
  submitted: boolean = false;
  ngOnInit(): void {
    this.activatedRoute.queryParams.subscribe(param=>{
      this.uuid = param.uuid
      this.checkMode(this.uuid);
    })
    this.bkPointCategoryService.getListCategoryMultiselect().subscribe(data=>{
      // console.log(data)
      this.listCategory = data;
    })

    if(this.mode === Mode.UPDATE){
      this.bkPointProductService.getProductDetail(this.uuid).subscribe(data=>{
        this.name = data.product_name;
        this.description = data.product_description;
        this.activeOnlineStore = data.is_active;
        this.price = data.price;
        this.selectedCategory = data.category;
        this.myProfileImgUrl = this.storageUrl + data.image;
        this.coverphotokey = data.image;
      })
    }
  }

  back(){
    this.location.back()
  }

  checkMode(uuid){
    if(CheckNullOrUndefinedOrEmpty(uuid)){
      this.mode = Mode.CREATE;
    }else{
      this.mode = Mode.UPDATE;
    }
  }

  createProduct(){
    this.submitted = true;
    if(CheckNullOrUndefinedOrEmpty(this.name) || CheckNullOrUndefinedOrEmpty(this.description)
      || CheckNullOrUndefinedOrEmpty(this.selectedCategory) || CheckNullOrUndefinedOrEmpty(this.price)
    ){
      return;
    }else{
      const dialogNotifi = this.dialog.open(CommonDialogComponent, {
        width: "500px",
        data: {
          message: "Are you sure you want to create product?",
          title:
            "NOTIFICATION",
          colorButton: false
        },
      });
      dialogNotifi.afterClosed().subscribe((result)=>{
        if(result === true){
          let createProduct = new ProductForm();
          createProduct.product_name = this.name;
          createProduct.product_description = this.description;
          createProduct.is_active = this.activeOnlineStore;
          createProduct.price = this.price;
          createProduct.product_image = this.coverphotokey;
          if(!CheckNullOrUndefinedOrEmpty(this.selectedCategory)){
            createProduct.category = this.selectedCategory;
          }
          this.bkPointProductService.createProduct(createProduct).subscribe(data=>{
            if(data.code === 200){
              const dialog = this.dialog.open(CommonDialogComponent, {
                width: "500px",
                data: {
                  message: "Create product successfully.",
                  title:
                    "NOTIFICATION",
                  colorButton: false
                },
              });
              dialog.afterClosed().subscribe(()=>{
                this.router.navigate(['/bk-point/product'])
              })
            }
          })
        }})
    }
  }

  updateProduct(){
    this.submitted = true;
    if(CheckNullOrUndefinedOrEmpty(this.name) || CheckNullOrUndefinedOrEmpty(this.description)
    || CheckNullOrUndefinedOrEmpty(this.selectedCategory) || CheckNullOrUndefinedOrEmpty(this.price)
  ){
    return;
  }else{
    const dialogNotifi = this.dialog.open(CommonDialogComponent, {
      width: "500px",
      data: {
        message: "Are you sure you want to update product?",
        title:
          "NOTIFICATION",
        colorButton: false
      },
    });
    dialogNotifi.afterClosed().subscribe((result)=>{
      if(result === true){
        let createProduct = new ProductForm();
        createProduct.product_name = this.name;
        createProduct.product_description = this.description;
        createProduct.is_active = this.activeOnlineStore;
        createProduct.price = this.price;
        createProduct.product_image = this.coverphotokey;
        if(!CheckNullOrUndefinedOrEmpty(this.selectedCategory)){
          createProduct.category = this.selectedCategory;
        }
        this.bkPointProductService.updateProduct(this.uuid,createProduct).subscribe(data=>{
          if(data.code === 200){
            const dialog = this.dialog.open(CommonDialogComponent, {
              width: "500px",
              data: {
                message: "Update product successfully.",
                title:
                  "NOTIFICATION",
                colorButton: false
              },
            });
            dialog.afterClosed().subscribe(()=>{
              this.router.navigate(['/bk-point/product'])
            })
          }
        })
      }})
  }
  }

  onSelectFile(event): void {
    if (event.target.files && event.target.files[0]) {
      const file = event.target.files[0];
      if (file.type != "image/bmp" && file.type != "image/jpeg" && file.type != "image/jpg" && file.type != "image/png") {
        return;
      }
      if (file.size > this.maximumImageSize * 1024 * 1024) {
        const dialogNotifi = this.dialog.open(CommonDialogComponent, {
          width: "500px",
          data: {
            message:
              `Maximum File Size ${this.maximumImageSize} MB. Please choose another picture`,
            title:
              "NOTIFICATION",
            colorButton: false
          },
        });
        dialogNotifi.afterClosed().subscribe(data => {
  
        });
        return;
      }
      const reader = new FileReader();
      reader.readAsDataURL(event.target.files[0]);
      reader.onload = (event) => {
        let preSignedUrl: string;
        let productPhotoKey: string;
        this.bkPointProductService.getPreSignedUrl(Date.now().toString() + file.name, file.type).subscribe(response => {
          if (response.code === 200) {
            productPhotoKey = response.key;
            preSignedUrl = response.url;
            this.bkPointProductService.uploadImage(preSignedUrl, file.type, file).subscribe(
              response => {
                this.myProfileImgUrl = this.storageUrl + productPhotoKey;
              }
            );
            this.coverphotokey = productPhotoKey;
            return productPhotoKey;
          }
        });
      };
    }
  }

  onSelectedCategory(event){
    console.log(this.selectedCategory)
  }

}
