
import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpHeaders, HttpParams, HttpResponse, } from '@angular/common/http'
import { catchError, map, retry } from 'rxjs/operators';
import { Router } from '@angular/router';
import { ProductApi , ListProductApi ,SearchProductApi, GetPresignUrlProductApi , ListAllProductApi , ListAllProductApiByStore } from './backend-api';
import { ApiService } from './api.service'
import { Observable, throwError } from 'rxjs';
import { IGetRowsParams } from 'ag-grid-community';
import { CheckNullOrUndefinedOrEmpty, RenderAddress } from '../utils/common-function';
import { branchAccount, branchStatus, accountInfor } from '../models/branch-account.model';
import { branchStore, storeDetail, storeInfor } from '../models/branch-store.model';
import * as _ from 'moment'
import { StoreInformation } from '../models/bk-point/store.model';
import { formatCurrency, formatDate } from '@angular/common';
import { CategoryDetail, CategotyForm } from '../models/bk-point/category-bkpoint.model';
import { Product, ProductDetail, ProductForm } from '../models/bk-point/product-bkpoint.model';
import { environment } from 'environments/environment';


@Injectable({
    providedIn: 'root'
})
export class BkPointProductService
{

    constructor(
        private http: HttpClient,
        private router: Router,
        private api: ApiService
    ) { }
    
    storageUrl = environment.storageUrl;

    createProduct(formProduct): Observable<any> {
        return this.api.post(ProductApi , formProduct)
    }

    getListProductByBrand(){
        return this.api.get(ListProductApi).pipe(
            map(data=>{
                if(data.code === 200){
                    let listProduct;
                    listProduct = this.renderListProduct(data.data)       
                    return listProduct;
                }
            }), catchError(value => throwError(value))
        )
    }

    getListAllProduct(isBrandAcount){
        let url : string;
        if(isBrandAcount){
            url = ListAllProductApi;
        }else{
            url = ListAllProductApiByStore;
        }
        return this.api.get(url).pipe(
            map(data=>{
                // console.log(data)
                if(data.code === 200){
                    let listProduct;
                    listProduct = this.renderMultiselectProduct(data.data)       
                    return listProduct;
                }
            }), catchError(value => throwError(value))
        )
    }

    renderMultiselectProduct(data){
        let listProduct = [];
        if(!CheckNullOrUndefinedOrEmpty(data)){
            data.forEach(element => {
                listProduct.push({label: element.product_name , value: Number(element.id)})
            });
        }
        return listProduct;
    }

    getListProductByCategoty(categoryId , name): Observable<any>{
        let listProduct:any = [];
        let param = new HttpParams();
        if(!CheckNullOrUndefinedOrEmpty(name)){
            param = param.append("name" , name)
        }
        if(!CheckNullOrUndefinedOrEmpty(categoryId)){
            param = param.append("id" , categoryId)
        }
        if(this.api.isEnable()){
            return this.http.get<any>( SearchProductApi,{ headers: this.api.headers, params: param } ).pipe(
                map(data=>{
                    // console.log(data)
                    if(data.code === 200){
                        data.data.forEach(element => {
                            let product= new Product();
                            product.product_name = element.product_name;
                            product.uuid = element.uuid;
                            product.id = element.id;
                            product.image = this.storageUrl + element.product_image;
                            product.productPriceCaculate = Number(element.price);
                            product.price = formatCurrency(element.price,"en-US",
                            "VND ",
                            "code",
                            "0.0-2" )
                            listProduct.push(product)
                        });
                    }
                    return listProduct;
                }),catchError((data) => throwError(data)))

        }
    }

    getProductDetail(uuid){
        let param = new HttpParams();
        param = param.append('uuid', uuid);
        if(this.api.isEnable()){
            return this.http.get<any>(ProductApi,{headers : this.api.headers, params : param}).pipe(
                map(respone => {
                    if(respone.code === 200)
                    {
                        return  this.renderDataProduct(respone.data)
                    }
                }), catchError(value => throwError(value))
            );
        }
    }

    updateProduct(uuid , updateForm){
        let param = new HttpParams();
        param = param.append('uuid', uuid);
        if(this.api.isEnable()){
            return this.http.put<any>(ProductApi, updateForm,{headers : this.api.headers, params : param})
        }
    }

    renderDataProduct(data){
        if(!CheckNullOrUndefinedOrEmpty(data)){
            let product = new ProductForm()
            product.product_name = data.product_name;
            product.product_description = data.product_description;
            product.is_active = data.is_active;
            product.image = data.product_image;
            product.price = data.price;
            product.category = [];
            if(!CheckNullOrUndefinedOrEmpty(data.productCategory)){
                data.productCategory.forEach(element => {
                    product.category.push(Number(element.category_id))                
                });
            }
            return product;
        }
    }

    renderListProduct(data){
        // console.log(data)
        if(!CheckNullOrUndefinedOrEmpty(data)){
            let listProduct = [];
            data.forEach(element => {
                let item = new ProductDetail();
                item.product_name = element.product_name;
                item.product_description = element.product_description;
                item.image =this.storageUrl +  element.product_image;
                item.uuid = element.uuid;
                item.updatedAt = formatDate(element.updated_at,"dd/MM/yyyy hh:ss" , 'en-US')
                listProduct.push(item)
            });
            return listProduct;
        }
    }

    getPreSignedUrl(fileName: string, fileType: string)
    {
        const bodyObj = { name: fileName, type: fileType };
        return this.api.post(GetPresignUrlProductApi, bodyObj).pipe(retry(3), catchError(this.errorHandler));
    }

    uploadImage(url: string, contentType: string, file)
    {
        const headers = new HttpHeaders({ 'Content-Type': contentType });
        return this.http.put<any>(url, file, { headers: headers, reportProgress: true }).pipe(retry(3), catchError(this.errorHandler));
    }

    private errorHandler(error: HttpErrorResponse)
    {
        if (error.error instanceof ErrorEvent)
        {
            console.error('An error occured:', error.error.message);
        }
        else
        {
            console.error(
                `Back-end return code: ${error.status}\n` +
                `Body content: ${error.status}`
            );
        }

        return throwError(error.message || 'Server Error');
    }

}