
import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams, HttpResponse, } from '@angular/common/http'
import { catchError, map } from 'rxjs/operators';
import { Router } from '@angular/router';
import { GetListAccountByBranchApi, GetListStoreByBranchApi, 
    InitBranchInformationApi , StoreInformationApi , 
    GetListStoreByBrandApi, 
    ConfigBrand} from './backend-api';
import { ApiService } from './api.service'
import { Observable, throwError } from 'rxjs';
import { IGetRowsParams } from 'ag-grid-community';
import { CheckNullOrUndefinedOrEmpty, RenderAddress } from '../utils/common-function';
import { branchAccount, branchStatus, accountInfor } from '../models/branch-account.model';
import { branchStore, storeDetail, storeInfor } from '../models/branch-store.model';
import * as _ from 'moment'
import { StoreInformation } from '../models/bk-point/store.model';
import { formatDate } from '@angular/common';
@Injectable({
    providedIn: 'root'
})
export class BranchService
{

    constructor(
        private http: HttpClient,
        private router: Router,
        private api: ApiService
    ) { }

    getInitBranchInformation(branch_manager_uuid : string): Observable<any> {
        let param = new HttpParams();
        param = param.append('account_uuid', branch_manager_uuid);
        return this.http.get<any>(InitBranchInformationApi,{headers : this.api.headers, params : param}).pipe(
            map(respone => {
                if(respone.code === 200)
                {
                    return  respone.data
                }
            }), catchError(value => throwError(value))
        );
    }

    getListStoreByBrand(){
        return this.api.get(GetListStoreByBrandApi).pipe(
            map(data=>{
                if(data.code === 200){
                    let listStore;
                    listStore = this.renderListStore(data.data.store)       
                    return listStore;
                }
            }), catchError(value => throwError(value))
        )
    }

    renderListStore(data){
        let listStore = [];
        if(!CheckNullOrUndefinedOrEmpty(data)){
            data.forEach(element => {
                listStore.push({label: element.store_name , value: Number(element.id)})
            });
        }
        return listStore;
    }

    getListAccount(params: IGetRowsParams): Observable<any> {
        if(this.api.isEnable()){
            return this.http.get<any>(GetListAccountByBranchApi, { headers: this.api.headers })
            .pipe(
                map(data => {
                    let list_account = [];
                    if (data.code === 200 && !CheckNullOrUndefinedOrEmpty(data.data)) {
                        data.data.forEach((element) => {
                          let account = new branchAccount();
                          let account_detail = new accountInfor();
                          account_detail.email = element.email;
                          account_detail.phone_number =  element.phone_number;
                          account_detail.name = element.first_name;
                          account_detail.uuid = element.uuid;
                          let status_detail = new branchStatus();
                          status_detail.is_active = element.is_active;
                          status_detail.manager_at = CheckNullOrUndefinedOrEmpty(element.storeManager) ? null : element.storeManager.store_name; 
                          account.account = account_detail;
                          account.status = status_detail;
                          list_account.push(account);
                        });
                        return { data : list_account , count: data.data.length};
                    }else{
                        return {
                            data : [],
                            count : 0
                        }
                    }
                })
            );
        }
    }


    getListStore(params: IGetRowsParams): Observable<any> {
        if(this.api.isEnable()){
            return this.http.get<any>(GetListStoreByBranchApi, { headers: this.api.headers })
            .pipe(
                map(data => {
                    let list_store = [];
                    if (data.code === 200 && !CheckNullOrUndefinedOrEmpty(data.data)) {
                        data.data.forEach((element) => {
                          let store = new branchStore();
                          let store_detail = new storeDetail();
                          store_detail.name = element.store_name;
                          store_detail.create_at = _(element.created_at).format("DD-MM-YYYY") ;
                          let store_information = new storeInfor();
                        //   store_information.manager_by = element.managerBy.first_name;
                          store_information.address = RenderAddress(element.storeAddress) ;
                          store.store = store_detail;
                          store.information = store_information;
                          store.uuid = element.uuid;
                          list_store.push(store);
                        });
                        return { data : list_store , count: list_store.length};
                    }else{
                        return {
                            data : [],
                            count : 0
                        }
                    }
                })
            );
        }else{
            return null;
        }
    }

    getStoreDetail(uuid){
        let param = new HttpParams();
        param = param.append('uuid', uuid);
        if(this.api.isEnable()){
            return this.http.get<any>(StoreInformationApi,{headers : this.api.headers, params : param}).pipe(
                map(respone => {
                    if(respone.code === 200)
                    {
                        return  this.rederDataStoreInformation(respone.data)
                    }
                }), catchError(value => throwError(value))
            );
        }
    }

    updateConfigBrand(formUpdateConfig){
        return this.api.put(ConfigBrand , formUpdateConfig)
    }

    getBrandConfigDetail(){
        return this.api.get(ConfigBrand).pipe(
            map(data=>{
                if(data.code === 200){
                    // console.log(data)
                    return data.data
                }
            })
        )
    }

    rederDataStoreInformation(data){
        if(!CheckNullOrUndefinedOrEmpty(data)){
            let store = new StoreInformation()
            store.name = data.store_name;
            store.description = data.store_description;
            store.createdAt = formatDate(data.created_at, 'dd/MM/yyyy', "en-US" );
            store.isActive = data.is_active;
            store.uuid = data.uuid;
            store.image = data.store_photo_key;
            store.address = (!CheckNullOrUndefinedOrEmpty(data.storeAddress)) ? data.storeAddress.detail_address + ", " + data.storeAddress.district + ", " + data.storeAddress.city : "";
            if(!CheckNullOrUndefinedOrEmpty(data.store_manager)){
                data.store_manager.forEach(element => {
                    if(!CheckNullOrUndefinedOrEmpty(element.manager)){
                        store.listManager.push(element.manager.first_name);
                    }
                });
            }
            return store;
        }
    }
}