import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';

import { FuseSharedModule } from '@fuse/shared.module';
import { AgGridModule } from 'ag-grid-angular';

import { StoreComponent } from './store.component';
import { MaterialModule} from '../angular-material/material.module'
import { TitleModule} from 'app/main/common-component/title/title.module';
import { CreateStoreComponent } from './create-store/create-store.component'
import { ButtonLoadingModule } from 'app/main/common-component/button-loading/button-loading.module';
import { DropdownModule } from 'primeng/dropdown';
import { StoreInformationComponent } from './store-information/store-information.component';

const routes = [
    {
        path     : 'store',
        component: StoreComponent
    },
    {
        path     : 'store/create-store',
        component: CreateStoreComponent
    },
    {
        path     : 'store/store-information',
        component: StoreInformationComponent
    }
];

@NgModule({
    declarations: [
        StoreComponent,
        CreateStoreComponent,
        StoreInformationComponent
    ],
    imports     : [
        RouterModule.forChild(routes),
        TranslateModule,
        FuseSharedModule,
        MaterialModule,
        TitleModule,
        AgGridModule,
        ButtonLoadingModule,
        DropdownModule

    ],
    exports     : [
        StoreComponent
    ]
})

export class StoresModule
{
}
