import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Title } from 'app/core/enum/title';
import { Location, formatDate } from '@angular/common';
import { ReportsService } from 'app/core/service/report.service';
import { ConvertService } from 'app/core/service/convert.service';
import { CheckNullOrUndefinedOrEmpty } from 'app/core/utils/common-function';
import * as jwt_decode from 'jwt-decode';
import { BranchService } from 'app/core/service/branch.service';
import { BkPointProductService } from 'app/core/service/product-bkpoint.service';

@Component({
  selector: 'app-report',
  templateUrl: './report.component.html',
  styleUrls: ['./report.component.scss']
})
export class ReportComponent implements OnInit {

  constructor(
    private location : Location,
    private _formBuilder: FormBuilder,
    private reportsService: ReportsService,
    private convertService: ConvertService,
    private branchService: BranchService,
    private bkPointProductService: BkPointProductService
  ) { }

  title = Title.DOT;
  buttonName: string = 'EXPORT';
  active: boolean = false;
  startDay: string;
  endDay: Date = new Date();
  DateEndFormat: string;
  reportsForm: FormGroup;
  maxDateFrom: Date = new Date();
  minDateStart: Date = new Date();
  minDateEnd: Date  = new Date();
  isShow: boolean = false;
  listTypeReport;
  isBrandAcount: boolean = false;
  isDisabled: boolean = false;
  decoded: any;
  listStore=[];
  selectedStore = [];
  endDate: string;
  listStoreSelected: string;
  typeReportValue: string;
  totalPrice: number;
  submited: boolean = false;
  listProduct = [];
  selectedProduct = [];
  formatListProduct: string;
  ngOnInit(): void {
    this.listTypeReport = [
      {
        name: 'Total sales of store',
        value: 'total_sales'
      },
      {
        name: 'Voucher',
        value: 'voucher'
      }
    ]

    let token = localStorage.getItem('token');
    if (!CheckNullOrUndefinedOrEmpty(token)){
        this.decoded = jwt_decode(token); 
        if(!CheckNullOrUndefinedOrEmpty(this.decoded)){
          // console.log(this.decoded)
          this.isBrandAcount = this.decoded.is_brand_account;
        }
    }

    if(this.isBrandAcount){
      this.branchService.getListStoreByBrand().subscribe(data=>{
        this.listStore = data;
      })

    }

    this.bkPointProductService.getListAllProduct(this.isBrandAcount).subscribe(data=>{
      this.listProduct = data
    })
    
  }

  SelectedTypeReports(event){
    this.typeReportValue = event.value.value;
  }

  onChangeFrom(event){
    this.startDay = formatDate(event, "yyyy-MM-dd", "en-US");
    this.minDateEnd = event;
  }

  onChangeTo(event){
    this.endDay.setDate(event.getDate()+1)
    this.endDate = formatDate(this.endDay, "yyyy-MM-dd", "en-US");
    this.DateEndFormat = formatDate(event, "yyyy-MM-dd", "en-US");
  }

  selectedProducts(event){
    this.formatListProduct = this.changInArr(this.selectedProduct);
    // console.log(this.formatListProduct)
  }

  exports(){
    this.submited = true;
    if(CheckNullOrUndefinedOrEmpty(this.startDay) || CheckNullOrUndefinedOrEmpty(this.endDate) || 
      (this.isBrandAcount && CheckNullOrUndefinedOrEmpty(this.listStoreSelected))){
      return;
    }else{
      this.active = true;
      this.buttonName = 'Processing...'
      this.reportsService.getDataReportOrder(this.startDay , this.endDate , this.listStoreSelected, this.formatListProduct ,this.isBrandAcount).subscribe(data=>{
        if(!CheckNullOrUndefinedOrEmpty(data)){
          this.active = false;
          this.buttonName = 'EXPORT';
          let dataGet;
          if(this.typeReportValue === 'total_sales'){
            dataGet = this.reportsService.renderTotalSalesReports(data);
          }

          let result = dataGet.fileCSV
          this.totalPrice = dataGet.sumTotal;
          let productTotalPrice = dataGet.sumProductPrice;
          if (this.typeReportValue === 'total_sales') {
            this.convertService.downloadFile(result, 'Sales of stores from ' + this.startDay + ' to ' + this.DateEndFormat ,this.totalPrice, this.typeReportValue , productTotalPrice)
          }
        }
      })
    }
  }

  SelectedStore(event){
    this.listStoreSelected = this.changInArr(this.selectedStore);
  }

  back() {
    this.location.back();
  }

  changInArr(arr){
    if(!CheckNullOrUndefinedOrEmpty(arr)){
      if (arr.length > 0) {
        let merge = '('
        for (let index = 0; index < arr.length; index++) {
            if(index === 0){
                merge = merge +  arr[0];
            } else {
                merge = merge + ',' + arr[index]
            }
    
        }
        return merge + ')'
        } else {
          return;
        }
    }
  }


}
