import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';
import { MatButtonModule} from '@angular/material/button';
import { MatIconModule} from '@angular/material/icon'
import { FuseSharedModule } from '@fuse/shared.module';
import { ListPromotionComponent } from './list-promotion.component';
import { MaterialModule} from '../../angular-material/material.module'
import { TitleModule} from 'app/main/common-component/title/title.module';
import { ButtonLoadingModule } from 'app/main/common-component/button-loading/button-loading.module';
import { NgxMaterialTimepickerModule} from 'ngx-material-timepicker';
import { DropdownModule } from 'primeng/dropdown';
import { MultiSelectModule } from 'primeng/multiselect';

const routes = [
    {
        path: 'bk-point/list-promotion',
        component: ListPromotionComponent
    }
];

@NgModule({
    declarations: [
        ListPromotionComponent
    ],
    imports: [
        RouterModule.forChild(routes),

        TranslateModule,
        MaterialModule,
        FuseSharedModule,
        MatButtonModule,
        MatIconModule,
        TitleModule,
        ButtonLoadingModule,
        NgxMaterialTimepickerModule,
        DropdownModule,
        MultiSelectModule
    ],
    exports: [ListPromotionComponent]
})

export class ListPromotionModule
{
}