import { Component, OnInit } from '@angular/core';
import { Title } from 'app/core/enum/title';
import { environment } from 'environments/environment';
import { Location } from '@angular/common'
import { CommonDialogComponent } from 'app/main/common-dialog/common-dialog.component';
import { MatDialog } from '@angular/material/dialog';
import * as CITY from 'assets/city.json';
import { StoreService } from 'app/core/service/store-bkpoint.service';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { FormStore } from 'app/core/models/bk-point/store.model';
import { ActivatedRoute, Router } from '@angular/router';
import { CheckNullOrUndefinedOrEmpty } from 'app/core/utils/common-function';
import { BkPointCategoryService } from 'app/core/service/category-bkpoint.service';

@Component({
  selector: 'app-create-store',
  templateUrl: './create-store.component.html',
  styleUrls: ['./create-store.component.scss']
})
export class CreateStoreComponent implements OnInit {

  constructor(
    private location : Location,
    public dialog: MatDialog,
    private storeService: StoreService,
    private formBuilder: FormBuilder,
    private router: Router,
    private activatedRoute : ActivatedRoute,
    private bkPointCategoryService : BkPointCategoryService,
  ) { }

  createStoreForm: FormGroup;

  title = Title.LEFT;
  storageUrl = environment.storageUrl;
  maximumImageSize : number = environment.imageSize;
  /** image url */
  myProfileImgUrl: string;

  coverphotokey : string;
  buttonName: string = "CREATE";

  activeStore: boolean = true;
  submited: boolean = false;
  city;
  selectedCity;
  uuid: string;
  mode: string;
  ngOnInit(): void {
    this.activatedRoute.queryParams.subscribe(param=>{
      this.uuid = param.uuid;
      this.chechMode(this.uuid);
    })
    this.city = (CITY as any).default;
    this.createStoreForm = this.formBuilder.group({
      store_name:['', Validators.required],
      store_description: ['',Validators.required],
      detail_address:['',Validators.required],
      district: ['', Validators.required],
      formManager: this.formBuilder.array([
        this.formBuilder.group({
          store_manager_name: ['',Validators.required],
          store_manager_phone: ['',Validators.required],
          store_manager_email: ['',Validators.required],
        })
      ]),
      image: []
    });

    if(this.mode === 'UPDATE'){
      this.storeService.getStoreDetaiiByUuid(this.uuid).subscribe(data=>{
        console.log(data)
        this.createStoreForm.patchValue({
          store_name: data.data.store_name,
          store_description: data.data.store_description,
          detail_address: (!CheckNullOrUndefinedOrEmpty(data.data.storeAddress))? data.data.storeAddress.detail_address: '',
          district: (!CheckNullOrUndefinedOrEmpty(data.data.storeAddress))? data.data.storeAddress.district: ''
          // formManager: data.managers

        })
        if(!CheckNullOrUndefinedOrEmpty(data.managers) && data.managers.length > 1){
          const newForm = this.formBuilder.group({
            store_manager_name: ['',Validators.required],
            store_manager_phone: ['',Validators.required],
            store_manager_email: ['',Validators.required],
          })
          const form = this.createStoreForm['controls'].formManager as FormArray;
          for(let i = 0; i < data.managers.length - 1; i++){
              form.push(newForm)
          }
        }
        this.createStoreForm.patchValue({
          formManager: data.managers
        })
        this.myProfileImgUrl = this.storageUrl + data.data.store_photo_key;
        this.coverphotokey = data.data.store_photo_key;
        this.activeStore = data.data.is_active;
        this.selectedCity = (!CheckNullOrUndefinedOrEmpty(data.data.storeAddress))? data.data.storeAddress.city: ''
      })
    }
  }

  chechMode(uuid){
    if(CheckNullOrUndefinedOrEmpty(this.uuid)){
      this.mode = 'CREATE';
    }else{
      this.mode = 'UPDATE';
    }
  }

  get f() { return this.createStoreForm.controls }; 

  back(){
    this.location.back()
  }
  onSelectFile(event): void {

    if (event.target.files && event.target.files[0]) {
      const file = event.target.files[0];
      if (file.type != "image/bmp" && file.type != "image/jpeg" && file.type != "image/jpg" && file.type != "image/png") {
        return;
      }
      if (file.size > this.maximumImageSize * 1024 * 1024) {
        const dialogNotifi = this.dialog.open(CommonDialogComponent, {
          width: "500px",
          data: {
            message:
              `Maximum File Size ${this.maximumImageSize} MB. Please choose another picture`,
            title:
              "NOTIFICATION",
            colorButton: false
          },
        });
        dialogNotifi.afterClosed().subscribe(data => {
  
        });
        return;
      }
      const reader = new FileReader();
      reader.readAsDataURL(event.target.files[0]);
      reader.onload = (event) => {
        let preSignedUrl: string;
        let storeImage: string;
        this.bkPointCategoryService.getPreSignedUrl(Date.now().toString() + file.name, file.type).subscribe(response => {
          if (response.code === 200) {
            // console.log(response)
            storeImage = response.key;
            preSignedUrl = response.url;
            this.bkPointCategoryService.uploadImage(preSignedUrl, file.type, file).subscribe(
              response => {
                this.myProfileImgUrl = this.storageUrl + storeImage;
              }
            );
            this.coverphotokey = storeImage;
            return storeImage;
          }
        });
      };
    }
  }

  createStore(){
    this.submited = true;
    if(this.createStoreForm.invalid){
      return;
    }else{
      const dialogNotifi = this.dialog.open(CommonDialogComponent, {
        width: "500px",
        data: {
          message: "Are you sure you want to create store?",
          title:
            "NOTIFICATION",
          colorButton: false
        },
      });
      dialogNotifi.afterClosed().subscribe((result)=>{
        if(result === true){
          this.buttonName = "Processing...";
          let create = new FormStore();
          create.store_name = this.createStoreForm.value.store_name;
          create.store_description = this.createStoreForm.value.store_description;
          create.detail_address = this.createStoreForm.value.detail_address;
          create.district = this.createStoreForm.value.district;
          create.city = this.selectedCity;
          create.is_active = this.activeStore;
          create.manager = this.createStoreForm.value.formManager
          create.store_photo_key = this.coverphotokey;

          // console.log(create)
          this.storeService.createStoreByBrand(create).subscribe(data=>{
            if(data.code === 200){
              this.buttonName = "CREATE";
              const dialog = this.dialog.open(CommonDialogComponent, {
                width: "500px",
                data: {
                  message: "Create store successfully.",
                  title:
                    "NOTIFICATION",
                  colorButton: false
                },
              });
              dialog.afterClosed().subscribe(()=>{
                // console.log('To be continue.')
                this.router.navigate(['/store'])
              })
            }else if(data.code === 202){
              this.buttonName = "CREATE";
              const dialog = this.dialog.open(CommonDialogComponent, {
                width: "500px",
                data: {
                  message: "Email is exist.",
                  title:
                    "NOTIFICATION",
                  colorButton: true
                },
              });
            }
          })
        }
      })

    }

  } 

  updateStore(){
    this.submited = true;
    if(this.createStoreForm.invalid){
      return;
    }else{
      const dialogNotifi = this.dialog.open(CommonDialogComponent, {
        width: "500px",
        data: {
          message: "Are you sure you want to update store?",
          title:
            "NOTIFICATION",
          colorButton: false
        },
      });
      dialogNotifi.afterClosed().subscribe((result)=>{
        if(result === true){
          let create = new FormStore();
          create.store_name = this.createStoreForm.value.store_name;
          create.store_description = this.createStoreForm.value.store_description;
          create.detail_address = this.createStoreForm.value.detail_address;
          create.district = this.createStoreForm.value.district;
          create.city = this.selectedCity;
          create.is_active = this.activeStore;
          create.manager = this.createStoreForm.value.formManager
          create.store_photo_key = this.coverphotokey;
          this.storeService.updateStore(create , this.uuid).subscribe(data=>{
            if(data.code === 200){
              const dialog = this.dialog.open(CommonDialogComponent, {
                width: "500px",
                data: {
                  message: "Update store successfully.",
                  title:
                    "NOTIFICATION",
                  colorButton: false
                },
              });
              dialog.afterClosed().subscribe(()=>{
                // console.log('To be continue.')
                this.router.navigate(['/store'])
              })
            }
          })
        }
      })
    }
  }

  checkSelectedDistrict(event){

  }

  addOrRemoveManager(action: string, index: number) {
    const newForm = this.formBuilder.group({
      store_manager_name: ['',Validators.required],
      store_manager_phone: ['',Validators.required],
      store_manager_email: ['',Validators.required],
    })

    const form = this.createStoreForm['controls'].formManager as FormArray
    if (action === 'add') {
      form.push(newForm);
    }
    else {
      if (form.length === 1) {
        return;
      }
      form.removeAt(index);
    }
  }
}
