import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';
import { MatButtonModule} from '@angular/material/button';
import { FuseSharedModule } from '@fuse/shared.module';
import { ConfigBrandComponent } from './config-brand.component'
import { MaterialModule} from '../../angular-material/material.module'
import { TitleModule} from 'app/main/common-component/title/title.module';
import { PlaceholderLoadingModule } from '../../common-component/placeholder-loading/placeholder-loading.module';
import { ButtonLoadingModule } from '../../common-component/button-loading/button-loading.module';
import { FooterPaymentModule } from '../select-payment/footer-payment/footer-payment.module';
import { MultiSelectModule } from 'primeng/multiselect';
import { CalendarModule } from 'primeng/calendar';
import { DropdownModule } from 'primeng/dropdown';
import { InputNumberModule } from 'primeng/inputnumber';
import { ConfigDetailComponent } from './config-detail/config-detail.component';

const routes = [
    {
        path: 'bk-point/config-brand',
        component: ConfigBrandComponent
    },
    {
        path: 'bk-point/config-detail',
        component: ConfigDetailComponent
    },

];

@NgModule({
    declarations: [
        ConfigBrandComponent,
        ConfigDetailComponent
    ],
    imports: [
        RouterModule.forChild(routes),

        TranslateModule,
        MaterialModule,
        FuseSharedModule,
        MatButtonModule,
        TitleModule,
        PlaceholderLoadingModule,
        ButtonLoadingModule,
        FooterPaymentModule,
        MultiSelectModule,
        CalendarModule,
        DropdownModule,
        InputNumberModule
    ],
    exports: [ConfigBrandComponent]
})

export class ConfigBrandModule
{
}