import { Component, OnInit } from '@angular/core';
import { Title } from 'app/core/enum/title';
import { VoucherService } from 'app/core/service/voucher.service';
import { environment } from 'environments/environment';

@Component({
  selector: 'app-list-promotion',
  templateUrl: './list-promotion.component.html',
  styleUrls: ['./list-promotion.component.scss']
})
export class ListPromotionComponent implements OnInit {

  constructor(
    private voucherService: VoucherService
  ) { }
  title = Title.LEFT_LINK;
  listVoucher = [];
  storeUrl = environment.storageUrl;
  ngOnInit(): void {
    this.voucherService.getListVoucherByBrand().subscribe(data=>{
      this.listVoucher = data;
    })
  }

}
