import { ChangeDetectionStrategy, ChangeDetectorRef, Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { merge, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import * as jwt_decode from 'jwt-decode';
import { filter } from 'rxjs/operators';

import { FuseNavigationService } from '@fuse/components/navigation/navigation.service';
import { isNullOrUndefined } from 'util';
import { Router, ActivatedRoute, NavigationEnd } from '@angular/router';
import { AuthService } from 'app/core/service/auth.service'
import { CheckNullOrUndefinedOrEmpty } from 'app/core/utils/common-function';

@Component({
    selector       : 'fuse-navigation',
    templateUrl    : './navigation.component.html',
    styleUrls      : ['./navigation.component.scss'],
    encapsulation  : ViewEncapsulation.None,
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class FuseNavigationComponent implements OnInit
{
    @Input()
    layout = 'vertical';

    @Input()
    navigation: any;

    navigations = []

    decoded: any;
    // Private
    private _unsubscribeAll: Subject<any>;

    checkRecruitment: boolean = true

    /**
     *
     * @param {ChangeDetectorRef} _changeDetectorRef
     * @param {FuseNavigationService} _fuseNavigationService
     */
    constructor(
        private _changeDetectorRef: ChangeDetectorRef,
        private _fuseNavigationService: FuseNavigationService,
    )
    {
        // Set the private defaults
        let token = localStorage.getItem('token');
        this._unsubscribeAll = new Subject();
        let url: string
        let tokenParam: any
        let decrypt: any
        let isRegistered
        // get url check questionnaire
        // this.router.events.pipe(
        //     filter(event => event instanceof NavigationEnd)
        // ).subscribe(() => {
        //     url = this.activatedRoute.snapshot['_routerState'].url
        // });
    }

    // -----------------------------------------------------------------------------------------------------
    // @ Lifecycle hooks
    // -----------------------------------------------------------------------------------------------------

    /**
     * On init
     */
    ngOnInit(): void
    {
        let token = localStorage.getItem('token');
        if (!isNullOrUndefined(token)){
            this.decoded = jwt_decode(token); 
        }
        // Load the navigation either from the input or from the service
        this.navigation = this.navigation || this._fuseNavigationService.getCurrentNavigation();

        // Subscribe to the current navigation changes
        this._fuseNavigationService.onNavigationChanged
            .pipe(takeUntil(this._unsubscribeAll))
            .subscribe(() => {

                // Load the navigation
                this.navigation = this._fuseNavigationService.getCurrentNavigation();
                // this.naepService.checkRecruitment().subscribe( data => {
                //     this.checkRecruitment = data
                //     this.checkEnableByRole();
                // })
                this.checkMenu()
                // // Mark for check
                // this._changeDetectorRef.markForCheck();
            });

        // Subscribe to navigation item
        merge(
            this._fuseNavigationService.onNavigationItemAdded,
            this._fuseNavigationService.onNavigationItemUpdated,
            this._fuseNavigationService.onNavigationItemRemoved
        ).pipe(takeUntil(this._unsubscribeAll))
         .subscribe(() => {

             // Mark for check
             this._changeDetectorRef.markForCheck();
         });
    }

    checkEnableByRole(){
        // await this.naepService.checkRecruitment().subscribe( data => {
            let arrRemove = [];
            let isShow: boolean = true;
            let token = localStorage.getItem('token');
            // this.checkMenu()
            
            if(!CheckNullOrUndefinedOrEmpty(token)){
                this.checkMenu()
            }

            // } else {
            //     this.checkRecruitment = false
            //     this.checkMenu()
            // }
            
          
        // })
  
    }

    checkMenu(){
        let arrRemove = [];
        let isShow: boolean = true;
        this.navigation.forEach(item => {
            isShow = true;

            // console.log(this.decoded.is_brand_account)

            if(CheckNullOrUndefinedOrEmpty(this.decoded)){
                return;
            }else{
                if(!this.decoded.is_brand_account){
                    if (item.id === 'store'){
                        item.hidden = true;
                    }
                    if (item.id === 'menu'){
                        item.children.forEach(element => {
                            if(element.id === 'product_manager'){
                                element.hidden = true;
                            }
                            else if(element.id === 'list_product'){
                                element.hidden = false;
                            }
                        });
                    }
                    if(item.id === 'promotion'){
                        item.children.forEach(element => {
                            if(element.id === 'promotion_configuration'){
                                element.hidden = true;
                            }else if(element.id === 'list_promotion'){
                                element.hidden = false;
                            }
                        });
                    }
                    if (item.id === 'config'){
                        item.hidden = true;
                    }
                }else{
                    if (item.id === 'store'){
                        item.hidden = false;
                    }
                    if(item.id === 'menu'){
                        item.children.forEach(element => {
                            if(element.id === 'list_product'){
                                element.hidden = true;
                            }else if(element.id === 'product_manager'){
                                element.hidden = false;
                            }
                        });
                    }
                    if (item.id === 'config'){
                        item.hidden = false;
                    }
                }
            }
        });  
        let arr = this.navigation.filter(function(item){
            return arrRemove.indexOf(item.id) === -1;
        });
        this.navigations = arr;
        // Mark for check
        this._changeDetectorRef.markForCheck();
    }


}
