import { Component, OnInit } from '@angular/core';
import { Title } from 'app/core/enum/title';
import { ProductDetail } from 'app/core/models/bk-point/product-bkpoint.model';
import { BkPointCategoryService } from 'app/core/service/category-bkpoint.service';
import { BkPointProductService } from 'app/core/service/product-bkpoint.service';

@Component({
  selector: 'app-product',
  templateUrl: './product.component.html',
  styleUrls: ['./product.component.scss']
})
export class ProductComponent implements OnInit {

  constructor(
    private bkPointProductService  : BkPointProductService,
  ) { }
  title = Title.DOT;
  listProduct: Array<ProductDetail>

  ngOnInit(): void {
    this.bkPointProductService.getListProductByBrand().subscribe(data=>{
      this.listProduct = data
    })
  }

}
