import { Component, NgZone, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { Title } from 'app/core/enum/title';
import { CommonDialogComponent } from 'app/main/common-dialog/common-dialog.component';
import { BehaviorSubject } from 'rxjs';
import { BarcodeFormat } from '@zxing/library';
import { OrderService } from 'app/core/service/order.service';
@Component({
  selector: 'app-scan-bar-code',
  templateUrl: './scan-bar-code.component.html',
  styleUrls: ['./scan-bar-code.component.scss']
})
export class ScanBarCodeComponent implements OnInit {

  constructor(
    private router: Router,
    public dialog: MatDialog,
    private activatedRoute: ActivatedRoute,
    private orderService: OrderService,
    private ngZone: NgZone,

  ) { }

  title = Title.LEFT;
  availableDevices: MediaDeviceInfo[];
  currentDevice: MediaDeviceInfo = null;
  hasDevices: boolean;
  qrValue: string;
  hasPermission: boolean;
  qrResultString: string;
  resultCode: number;
  torchEnabled = false;
  torchAvailable$ = new BehaviorSubject<boolean>(false);
  tryHarder = false;
  order_id : number;
  isScan: boolean = true;
  uuid: string;
  mode: string;
  customer_id: number  = 1;
  value: string = 'hahane';
  formatsEnabled: BarcodeFormat[] = [
    BarcodeFormat.CODE_128,
    BarcodeFormat.DATA_MATRIX,
    BarcodeFormat.EAN_13,
    BarcodeFormat.QR_CODE,
  ];
  ngOnInit(): void {
    this.activatedRoute.queryParams.subscribe((params) => {
      this.order_id = params.id;
      this.uuid = params.uuid;
      this.mode = params.mode
    });
  }

  onCodeResult(resultString)
  {
    console.log(resultString)
    if(this.isScan){
      this.isScan = false;
      const dialogNotifi = this.dialog.open(CommonDialogComponent, {
        width: "500px",
        data: {
          message:
            "Scan successfully.",
          title:
            "NOTIFICATION",
          colorButton: false
        },
      });
      dialogNotifi.afterClosed().subscribe(response =>
      {
        if(this.mode === 'sc'){
          this.orderService.updateOrderCustomerInfoAfterScan(resultString , this.order_id).subscribe(
            data => {
              if(data.code === 200){
                this.ngZone.run(() => this.router.navigate(['bk-point/order-summary'],{ queryParams: { uuid: this.uuid} })).then();
              }
            }
          )

        }else if(this.mode === 'dc'){
          let response = JSON.parse(resultString)
          let brand_id = response['brand_id'];
          let voucherValue = response['voucher_value'];
          this.orderService.updateOrderAfterScanVoucher(this.uuid , brand_id , voucherValue ).subscribe(data=>{
            console.log(data)
            if(data.code === 200){
              this.ngZone.run(() => this.router.navigate(['bk-point/order-summary'],{ queryParams: { uuid: this.uuid} })).then();
            }else if(data.code === 202){
              const dialogNotifi = this.dialog.open(CommonDialogComponent, {
                width: "500px",
                data: {
                  message:
                    "This voucher is of another brand.",
                  title:
                    "NOTIFICATION",
                  colorButton: true
                },
              });
            }
          })
        }
        //Check thuoc brand khong
        //Luu is_use
        //Giam gia tien
      })
    }
  }

  onCamerasFound(devices: MediaDeviceInfo[]): void
  {
    this.availableDevices = devices;
    this.hasDevices = Boolean(devices && devices.length);
  }

  clearResult(): void
  {
    this.qrResultString = '';
    this.isScan = true;
  }

  onDeviceSelectChange(selected: string)
  {
    const device = this.availableDevices.find(x => x.deviceId === selected);
    this.currentDevice = device || null;
  }

  onHasPermission(has: boolean)
  {
    this.hasPermission = has;
  }

  onTorchCompatible(isCompatible: boolean): void
  {
    this.torchAvailable$.next(isCompatible || false);
  }

  toggleTorch(): void
  {
    this.torchEnabled = !this.torchEnabled;
  }

  toggleTryHarder(): void
  {
    this.tryHarder = !this.tryHarder;
  }

}
