
import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpHeaders, HttpParams, HttpResponse, } from '@angular/common/http'
import { catchError, map, retry } from 'rxjs/operators';
import { Router } from '@angular/router';
import { CategoryApi , GetPresignUrlCategoryApi, ListCategoryApi , SearchCategoryApi} from './backend-api';
import { ApiService } from './api.service'
import { Observable, throwError } from 'rxjs';
import { IGetRowsParams } from 'ag-grid-community';
import { CheckNullOrUndefinedOrEmpty, RenderAddress } from '../utils/common-function';
import { branchAccount, branchStatus, accountInfor } from '../models/branch-account.model';
import { branchStore, storeDetail, storeInfor } from '../models/branch-store.model';
import * as _ from 'moment'
import { StoreInformation } from '../models/bk-point/store.model';
import { formatDate } from '@angular/common';
import { CategoryDetail, CategoryStore, CategotyForm } from '../models/bk-point/category-bkpoint.model';
import { environment } from 'environments/environment';

@Injectable({
    providedIn: 'root'
})
export class BkPointCategoryService
{

    constructor(
        private http: HttpClient,
        private router: Router,
        private api: ApiService
    ) { }
    
    storageUrl = environment.storageUrl;

    createCategory(formCategory): Observable<any> {
        return this.api.post(CategoryApi , formCategory)
    }

    getListCategoryByBrand(){
        return this.api.get(ListCategoryApi).pipe(
            map(data=>{
                if(data.code === 200){
                    let listCategory;
                    listCategory = this.renderListCategory(data.data)       
                    return listCategory;
                }
            }), catchError(value => throwError(value))
        )
    }

    getListCategoryMultiselect(){
        return this.api.get(ListCategoryApi).pipe(
            map(data=>{
                if(data.code === 200){
                    let listCategory;
                    listCategory = this.renderListCategoryMultiselect(data.data)       
                    return listCategory;
                }
            }), catchError(value => throwError(value))
        )
    }

    searchCategorys(searchString: string): Observable<any>{
        let products = [];
        let param = new HttpParams();
        if (!CheckNullOrUndefinedOrEmpty(searchString)) {
            param = param.append('name', searchString);
        }
        if(this.api.isEnable()){
            return this.http.get<any>(SearchCategoryApi, { headers: this.api.headers, params: param }).pipe(
                map((value) => {
                    if (value.code === 200) {
                        // console.log(value)
                        value.data.forEach(element => {
                            let category = new CategoryStore();
                            category.id = element.id;
                            category.name = element.category_name;
                            category.image = this.storageUrl + element.category_photo_key;
                            products.push(category);
                    })
                    return products;
                }
                }), catchError(value => throwError(value))
            );
            
        }
    }

    renderListCategoryMultiselect(data){
        if(!CheckNullOrUndefinedOrEmpty(data)){
            let listCategory = [];
            data.forEach(element => {
                listCategory.push({label: element.category_name , value: Number(element.id)})
            });
            return listCategory;
        }
    }

    renderListCategory(data){
        // console.log(data)
        if(!CheckNullOrUndefinedOrEmpty(data)){
            let listCategory = [];
            data.forEach(element => {
                let item = new CategoryDetail();
                item.name = element.category_name;
                item.description = element.category_description;
                item.image = this.storageUrl +  element.category_photo_key;
                item.uuid = element.uuid;
                item.updatedAt = formatDate(element.updated_at,"dd/MM/yyyy hh:ss" , 'en-US')
                listCategory.push(item)
            });
            return listCategory;
        }
    }

    updateCategory(uuid, formUpdate){
        let param = new HttpParams();
        param = param.append('uuid', uuid);
        if(this.api.isEnable()){
            return this.http.put<any>(CategoryApi,formUpdate,{headers : this.api.headers, params : param});
        }
    }

    getCategoryDetail(uuid){
        let param = new HttpParams();
        param = param.append('uuid', uuid);
        if(this.api.isEnable()){
            return this.http.get<any>(CategoryApi,{headers : this.api.headers, params : param}).pipe(
                map(respone => {
                    if(respone.code === 200)
                    {
                        return  this.renderDataCategory(respone.data)
                    }
                }), catchError(value => throwError(value))
            );
        }
    }

    renderDataCategory(data){
        // console.log(data)
        if(!CheckNullOrUndefinedOrEmpty(data)){
            let category = new CategotyForm()
            category.category_description = data.category_description;
            category.category_name = data.category_name;
            category.is_active = data.is_active;
            category.category_photo_key = data.category_photo_key;
            return category;
        }
    }
    getPreSignedUrl(fileName: string, fileType: string)
    {
        const bodyObj = { name: fileName, type: fileType };
        return this.api.post(GetPresignUrlCategoryApi, bodyObj).pipe(retry(3), catchError(this.errorHandler));
    }

    uploadImage(url: string, contentType: string, file)
    {
        const headers = new HttpHeaders({ 'Content-Type': contentType });
        return this.http.put<any>(url, file, { headers: headers, reportProgress: true }).pipe(retry(3), catchError(this.errorHandler));
    }

    private errorHandler(error: HttpErrorResponse)
    {
        if (error.error instanceof ErrorEvent)
        {
            console.error('An error occured:', error.error.message);
        }
        else
        {
            console.error(
                `Back-end return code: ${error.status}\n` +
                `Body content: ${error.status}`
            );
        }

        return throwError(error.message || 'Server Error');
    }
}