import { Component, OnInit } from '@angular/core';
import { Title } from 'app/core/enum/title';
import { environment } from 'environments/environment';
import { Location } from '@angular/common'
import { CommonDialogComponent } from 'app/main/common-dialog/common-dialog.component';
import { MatDialog } from '@angular/material/dialog';
import * as jwt_decode from 'jwt-decode';
import { CheckNullOrUndefinedOrEmpty } from 'app/core/utils/common-function';
import { BranchService } from 'app/core/service/branch.service';
import { CategotyForm } from 'app/core/models/bk-point/category-bkpoint.model';
import { BkPointCategoryService } from 'app/core/service/category-bkpoint.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Mode } from 'app/core/enum/mode';
@Component({
  selector: 'app-create-category',
  templateUrl: './create-category.component.html',
  styleUrls: ['./create-category.component.scss']
})
export class CreateCategoryComponent implements OnInit {

  constructor(
    private location : Location,
    public dialog: MatDialog,
    private branchService : BranchService,
    private bkPointCategoryService : BkPointCategoryService,
    private activatedRoute : ActivatedRoute,
    private router: Router,
  ) { }
  title = Title.LEFT;

  storageUrl = environment.storageUrl;

  maximumImageSize : number = environment.imageSize;
  /** image url */
  myProfileImgUrl: string;
  
  coverphotokey : string;
  buttonName: string = "CREATE";
  submmited: boolean = false;
  //Data 
  activeOnlineStore: boolean = true;
  name: string;
  description: string;
  decoded;
  is_brand_account: boolean;
  selectedStore;
  listStore;
  submitted: boolean = false;
  uuid: string;
  mode: string;
  ngOnInit(): void {
    let token = localStorage.getItem('token');
    if (!CheckNullOrUndefinedOrEmpty(token)){
        this.decoded = jwt_decode(token); 
        this.is_brand_account = this.decoded.is_brand_account;
    }

    this.activatedRoute.queryParams.subscribe(param=>{
      this.uuid = param.uuid
      this.checkMode(this.uuid);
    })

    // if(this.is_brand_account){
    //   this.branchService.getListStoreByBrand().subscribe(data=>{
    //    
    //     this.listStore = data;
    //   })
    // }

    if(this.mode === Mode.UPDATE){
      this.bkPointCategoryService.getCategoryDetail(this.uuid).subscribe(data=>{
        this.name = data.category_name;
        this.description = data.category_description;
        this.activeOnlineStore = data.is_active;
        this.coverphotokey = data.category_photo_key;
        this.myProfileImgUrl = this.storageUrl + data.category_photo_key;
      })
    }
  }

  back(){
    this.location.back()
  }

  checkMode(uuid){
    if(CheckNullOrUndefinedOrEmpty(uuid)){
      this.mode = Mode.CREATE;
    }else{
      this.mode = Mode.UPDATE;
    }
  }

  createCategory(){
    this.submitted = true;
    if(CheckNullOrUndefinedOrEmpty(this.name) || CheckNullOrUndefinedOrEmpty(this.description)
    ){
      return;
    }else{
      const dialogNotifi = this.dialog.open(CommonDialogComponent, {
        width: "500px",
        data: {
          message: "Are you sure you want to create category?",
          title:
            "NOTIFICATION",
          colorButton: false
        },
      });
      dialogNotifi.afterClosed().subscribe((result)=>{
        if(result === true){
          let createCategory = new CategotyForm();
          createCategory.category_name = this.name;
          createCategory.category_description = this.description;
          createCategory.is_active = this.activeOnlineStore;
          createCategory.category_photo_key = this.coverphotokey;
          // if(this.is_brand_account && !CheckNullOrUndefinedOrEmpty(this.selectedStore)){
          //   createCategory.store_id = this.selectedStore;
          // }
          this.bkPointCategoryService.createCategory(createCategory).subscribe(data=>{
            if(data.code === 200){
              const dialog = this.dialog.open(CommonDialogComponent, {
                width: "500px",
                data: {
                  message: "Create category successfully.",
                  title:
                    "NOTIFICATION",
                  colorButton: false
                },
              });
              dialog.afterClosed().subscribe(()=>{
                this.router.navigate(['/bk-point/category'])
              })
            }
          })
        }})
    }

  }

  updateCategory(){
    this.submitted = true;
    if(CheckNullOrUndefinedOrEmpty(this.name) || CheckNullOrUndefinedOrEmpty(this.description)
    ){
      return;
    }else{
      const dialogNotifi = this.dialog.open(CommonDialogComponent, {
        width: "500px",
        data: {
          message: "Are you sure you want to update category?",
          title:
            "NOTIFICATION",
          colorButton: false
        },
      });

      dialogNotifi.afterClosed().subscribe((result)=>{
        if(result === true){
          let updateCategory = new CategotyForm();
          updateCategory.category_name = this.name;
          updateCategory.category_description = this.description;
          updateCategory.is_active = this.activeOnlineStore;
          updateCategory.category_photo_key = this.coverphotokey;
          this.bkPointCategoryService.updateCategory(this.uuid ,updateCategory ).subscribe(data=>{
            if(data.code === 200){
              const dialog = this.dialog.open(CommonDialogComponent, {
                width: "500px",
                data: {
                  message: "Create category successfully.",
                  title:
                    "NOTIFICATION",
                  colorButton: false
                },
              });
              dialog.afterClosed().subscribe(()=>{
                // console.log('To be continue.')
                this.router.navigate(['/bk-point/category'])
              })
            }
          })
        }})

    }
  }

  onSelectFile(event): void {
   
    if (event.target.files && event.target.files[0]) {
      const file = event.target.files[0];
      if (file.type != "image/bmp" && file.type != "image/jpeg" && file.type != "image/jpg" && file.type != "image/png") {
        return;
      }
      if (file.size > this.maximumImageSize * 1024 * 1024) {
        const dialogNotifi = this.dialog.open(CommonDialogComponent, {
          width: "500px",
          data: {
            message:
              `Maximum File Size ${this.maximumImageSize} MB. Please choose another picture`,
            title:
              "NOTIFICATION",
            colorButton: false
          },
        });
        dialogNotifi.afterClosed().subscribe(data => {
  
        });
        return;
      }
      const reader = new FileReader();
      reader.readAsDataURL(event.target.files[0]);
      reader.onload = (event) => {
        let preSignedUrl: string;
        let categoryImage: string;
        this.bkPointCategoryService.getPreSignedUrl(Date.now().toString() + file.name, file.type).subscribe(response => {
          if (response.code === 200) {
            // console.log(response)
            categoryImage = response.key;
            preSignedUrl = response.url;
            this.bkPointCategoryService.uploadImage(preSignedUrl, file.type, file).subscribe(
              response => {
                this.myProfileImgUrl = this.storageUrl + categoryImage;
              }
            );
            this.coverphotokey = categoryImage;
            return categoryImage;
          }
        });
      };
    }
  }

  onSelectedStore(event){
    console.log(this.selectedStore)
  }

}
