import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';
import { MatButtonModule} from '@angular/material/button';
import { FuseSharedModule } from '@fuse/shared.module';
import { OrderSummaryComponent } from './order-summary.component'
import { MaterialModule} from '../../angular-material/material.module'
import { TitleModule} from 'app/main/common-component/title/title.module';
import { PlaceholderLoadingModule } from '../../common-component/placeholder-loading/placeholder-loading.module';
import { ButtonLoadingModule } from '../../common-component/button-loading/button-loading.module';
import { FooterPaymentModule } from '../select-payment/footer-payment/footer-payment.module';

const routes = [
    {
        path: 'bk-point/order-summary',
        component: OrderSummaryComponent
    },

];

@NgModule({
    declarations: [
        OrderSummaryComponent
    ],
    imports: [
        RouterModule.forChild(routes),

        TranslateModule,
        MaterialModule,
        FuseSharedModule,
        MatButtonModule,
        TitleModule,
        PlaceholderLoadingModule,
        ButtonLoadingModule,
        FooterPaymentModule
    ],
    exports: [OrderSummaryComponent]
})

export class OrderSummaryModule
{
}