import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';

import { FuseSharedModule } from '@fuse/shared.module';
import { AgGridModule } from 'ag-grid-angular';

import { StoreAccountComponent } from './store-account.component';
// import { ProductsModule } from './products/products.module';
import { MaterialModule} from '../angular-material/material.module'
import { TitleModule} from 'app/main/common-component/title/title.module';
import { CreateAccuntComponent } from './create-accunt/create-accunt.component'

const routes = [
    {
        path     : 'store-account',
        component: StoreAccountComponent
    },
    {
        path     : 'store-account/create-store-account',
        component: CreateAccuntComponent
    }
];

@NgModule({
    declarations: [
        StoreAccountComponent,
        CreateAccuntComponent
    ],
    imports     : [
        RouterModule.forChild(routes),
        TranslateModule,
        FuseSharedModule,
        // ProductsModule,
        MaterialModule,
        TitleModule,
        AgGridModule

    ],
    exports     : [
        StoreAccountComponent
    ]
})

export class StoreAccountModule
{
}
