import { Injectable } from "@angular/core";
import { ApiService } from "./api.service";
import { HttpClient, HttpParams } from "@angular/common/http";
import { reportOrderOfBrand, reportOrderOfStore } from './backend-api';
import { Observable, throwError } from 'rxjs';
import { catchError, map, retry } from 'rxjs/operators';
import { Report } from './convert.service';
import { formatDate } from '@angular/common';
import * as moment from 'moment';
import { CheckNullOrUndefinedOrEmpty } from "../utils/common-function";


@Injectable({
  providedIn: "root",
})
export class ReportsService {
  constructor(
    private api: ApiService,
    private http: HttpClient
    ) {}

    getDataReportOrder(start , end , stores , product ,isBrandAccount){
        let param = new HttpParams();
        param = param.append('start', start);
        param = param.append('end', end);
        param = param.append('product', product);
        let url: string;
        if(isBrandAccount){
            url = reportOrderOfBrand;
            param = param.append('store', stores);
        }else{
            url = reportOrderOfStore;
        }
        if(this.api.isEnable()){
            return this.http.get<any>(url  ,{headers : this.api.headers, params : param}).pipe(
                map(data=>{
                    // console.log(data)
                    if(data.code === 200){
                        return data.data
                    }
                })
            )
        }
    }

    renderTotalSalesReports(data){
        let fileCSV = [];
        let sumTotal: number = 0;
        let sumProductPrice: number = 0;
        if(!CheckNullOrUndefinedOrEmpty(data)){
            data.forEach(element => {

                sumTotal += Number(element.total_price)
                element.orderLineItem.forEach(item => {
                    
                    let report = new Report();
                    report.orderID = element.id;
                    report.orderDay = formatDate(element.created_at , 'dd/MM/yyyy', 'en-US');
                    if(!CheckNullOrUndefinedOrEmpty(element.store)){
                        report.storeID = element.store.id;
                        report.storeName = element.store.store_name;
                    }
                    report.order_price = element.total_price;
                    if(!CheckNullOrUndefinedOrEmpty(element.customer)){
                        report.customerName = element.customer.first_name + element.customer.last_name;
                        report.phoneNumber = '="' + element.customer.phone_number + '"';
                    }
                    
                    if(!CheckNullOrUndefinedOrEmpty(element.customerVoucher) && !CheckNullOrUndefinedOrEmpty(element.customerVoucher.voucher)){
                        report.discount = element.customerVoucher.voucher.percent_discount + ' %'
                    }else{
                        report.discount = '';
                    }

                    report.productName = item.product_name;
                    report.product_price = item.price;
                    sumProductPrice += Number(item.price) * Number(item.quantity)
                    report.quantity = item.quantity;
                    fileCSV.push(report)
                });
            });
        }
        return {fileCSV, sumTotal , sumProductPrice};
    }

    
}