import { Component, NgZone, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { Title } from 'app/core/enum/title';
import { OrderPay } from 'app/core/models/bk-point/order-bkpoint.model';
import { SharedService } from 'app/core/service/commom/shared.service';
import { OrderService } from 'app/core/service/order.service';
import { CheckNullOrUndefinedOrEmpty } from 'app/core/utils/common-function';
import { CommonDialogComponent } from 'app/main/common-dialog/common-dialog.component';
import { environment } from 'environments/environment';

@Component({
  selector: 'app-order-summary',
  templateUrl: './order-summary.component.html',
  styleUrls: ['./order-summary.component.scss']
})
export class OrderSummaryComponent implements OnInit {

  constructor(
    private translateService: TranslateService,
    private router: Router,
    public dialog: MatDialog,
    private activatedRoute: ActivatedRoute,
    private orderService: OrderService,
    private ngZone: NgZone,
    private sharedService: SharedService,
  ) { }

  carArr=[];
  Total: number;
  title = Title.LEFT;

  buttonName = "PAY";
  isSubmitRemark: boolean = true;
  active: boolean = false;
  loading: boolean = true;
  storeUrl = environment.storageUrl;
  //Customer Infor
  info_customer_first_name: string = 'Sang';
  info_customer_last_name: string ='Quốc';
  info_customer_email: string = 'sang2@gmail.com';
  info_customer_phone_dial_code: string ='85';
  info_customer_phone_number: string = '123456789';

  //uuid
  uuid: string;
  discount: number = 0;
  orderId : number;
  customerId: number;
  subtotal: number = 0;
  total: number =0;
  pointPay: number = 0;
  pointChange: number;
  ngOnInit(): void {
    this.activatedRoute.queryParams.subscribe((params) => {
      this.uuid = params.uuid
    });

    this.orderService.getorderByUuid(this.uuid).subscribe(data=>{
      console.log(data)
      this.carArr = data.item;
      if(!CheckNullOrUndefinedOrEmpty(data.dataOrder) && !CheckNullOrUndefinedOrEmpty(data.dataOrder.customer)){
        this.info_customer_email = data.dataOrder.customer.email;
        this.info_customer_last_name = data.dataOrder.customer.last_name;
        this.info_customer_first_name = data.dataOrder.customer.first_name;
        this.info_customer_phone_number = data.dataOrder.customer.phone_number;
        this.customerId =  data.dataOrder.customer.id
        
      }
      if(!CheckNullOrUndefinedOrEmpty(data.dataOrder)){
        this.Total = data.dataOrder.total_price
        this.pointChange = (!CheckNullOrUndefinedOrEmpty(data.dataOrder.store) && (!CheckNullOrUndefinedOrEmpty(data.dataOrder.store.branch))) ? Number(data.dataOrder.store.branch.point_to_money) : 0 ; 
        this.orderId = data.dataOrder.id;
        this.pointPay = Math.round(Number(this.Total) / this.pointChange)
        if(CheckNullOrUndefinedOrEmpty(data.dataOrder.customerVoucher)){
          this.subtotal = data.dataOrder.total_price;
        }else{

          this.discount = data.dataOrder.price_discount
          this.subtotal = Number(this.discount) + Number(this.Total);
          
          
        }
      }

    })
  }

  payOrder(){
    let formUpdate = new OrderPay();
    formUpdate.orderId = this.orderId;
    formUpdate.customerId = this.customerId;
    this.orderService.updateOrderAfterPay(formUpdate).subscribe(data=>{
      if(data.code === 200){
        const dialog = this.dialog.open(CommonDialogComponent, {
          width: "500px",
          data: {
            message: "Payment successfully.",
            title:
              "NOTIFICATION",
            colorButton: false
          },
        });
        dialog.afterClosed().subscribe(()=>{
          this.orderService.updateOrderSuccess(this.uuid).subscribe(data=>{
            if(data.code === 200){
              this.sharedService.nextCart(0);
              this.router.navigate(['/bk-point/menu'])
            }
          })
        })
      }
    })
  }

  payPoint(){
    let formUpdate = new OrderPay();
    formUpdate.orderId = this.orderId;
    formUpdate.customerId = this.customerId;
    formUpdate.points = this.pointPay;
    this.orderService.updateOrderByPoint(formUpdate).subscribe(data=>{
      if(data.code === 200){
        const dialog = this.dialog.open(CommonDialogComponent, {
          width: "500px",
          data: {
            message: "Payment successfully.",
            title:
              "NOTIFICATION",
            colorButton: false
          },
        });
        dialog.afterClosed().subscribe(()=>{
          this.orderService.updateOrderSuccess(this.uuid).subscribe(data=>{
            if(data.code === 200){
              this.sharedService.nextCart(0);
              this.router.navigate(['/bk-point/menu'])
            }
          })
        })
      }else if(data.code === 202){
        const dialog = this.dialog.open(CommonDialogComponent, {
          width: "500px",
          data: {
            message: "Point is not enough.",
            title:
              "NOTIFICATION",
            colorButton: true
          },
        });
      }
    })
  }

    // get static translation
    getStaticTranslation(key: string) {
      return this.translateService.getStreamOnTranslationChange(key);
    }

}
