export class ProductForm{
    product_name: string;
    product_description:string;
    is_active: boolean;
    image:string;
    price: number;
    promotional_price : number;

    promotion_start_time: Date;

    promotion_end_time: Date;

    properties: JSON;
    category : number[] ;
    product_image: string;
}

export class ProductDetail{
    product_name: string;
    product_description: string;
    updatedAt: string;
    image: string;
    uuid: string;
}

export class Product{
    id: number;
    uuid: string;
    product_name: string;
    price: string;
    image: string;
    currencyCode: 'VND';
    quantity: number = 0;
    productPriceCaculate: number;
    disableDecrease: boolean = true;
}