
import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams, HttpResponse, } from '@angular/common/http'
import { catchError, map } from 'rxjs/operators';
import { Router } from '@angular/router';
import { StoreApi , StoreDetailByUuidApi } from './backend-api';
import { ApiService } from './api.service'
import { Observable, throwError } from 'rxjs';
import { IGetRowsParams } from 'ag-grid-community';
import { CheckNullOrUndefinedOrEmpty, RenderAddress } from '../utils/common-function';
import { branchAccount, branchStatus, accountInfor } from '../models/branch-account.model';
import { branchStore, storeDetail, storeInfor, 
 } from '../models/branch-store.model';
import * as _ from 'moment'
import { ManagerOfStore } from '../models/bk-point/store.model';
@Injectable({
    providedIn: 'root'
})
export class StoreService
{

    constructor(
        private http: HttpClient,
        private router: Router,
        private api: ApiService
    ) { }

    createStoreByBrand(formStore){
        return this.api.post(StoreApi, formStore);
    }

    updateStore(formStore , uuid){
        let param = new HttpParams();
        param = param.append('store_uuid', uuid);
        if(this.api.isEnable()){
            return this.http.put<any>(StoreApi,formStore,{headers : this.api.headers, params : param})
        }
    }

    getStoreDetaiiByUuid(uuid){
        let param = new HttpParams();
        param = param.append('uuid', uuid);
        if(this.api.isEnable()){
            return this.http.get<any>(StoreDetailByUuidApi,{headers : this.api.headers, params : param}).pipe(
                map(data=>{
                    // console.log(data)
                    if(data.code === 200){
                        if(!CheckNullOrUndefinedOrEmpty(data.data.store_manager)){
                            let managers = [];
                            data.data.store_manager.forEach(element => {
                                let manager = new ManagerStoreIOnfo();
                                manager.store_manager_email =element.manager.email;
                                manager.store_manager_name = element.manager.first_name;
                                manager.store_manager_phone = element.manager.phone_number;
                                managers.push(manager)
                            });
                            return {data: data.data , managers: managers}
                          }
                    }
                }), catchError(value => throwError(value))
            )

        }
    }
}

export class ManagerStoreIOnfo{
    store_manager_name: string;
    store_manager_phone: string;
    store_manager_email: string;
}