
import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpHeaders, HttpParams, HttpResponse, } from '@angular/common/http'
import { catchError, map, retry } from 'rxjs/operators';
import { Router } from '@angular/router';
import { Order , OrderLineItem , UpdateOrderInfor , getOrderByUuid ,
            updateOrderSuccess , scanVoucher , dataPaymentOrder, PaymentOrderByPoint
        } from './backend-api';
import { ApiService } from './api.service'
import { Observable, throwError } from 'rxjs';
import { IGetRowsParams } from 'ag-grid-community';
import { CheckNullOrUndefinedOrEmpty, RenderAddress } from '../utils/common-function';
import { branchAccount, branchStatus, accountInfor } from '../models/branch-account.model';
import { branchStore, storeDetail, storeInfor } from '../models/branch-store.model';
import * as _ from 'moment'
import { StoreInformation } from '../models/bk-point/store.model';
import { formatCurrency, formatDate } from '@angular/common';
import { CategoryDetail, CategotyForm } from '../models/bk-point/category-bkpoint.model';
import { Product, ProductDetail, ProductForm } from '../models/bk-point/product-bkpoint.model';
import { environment } from 'environments/environment';
import { OrderDetail } from '../models/bk-point/order-bkpoint.model';


@Injectable({
    providedIn: 'root'
})
export class OrderService
{

    constructor(
        private http: HttpClient,
        private router: Router,
        private api: ApiService
    ) { }
    
    createOrder(formOrder){
        return this.api.post(Order, formOrder);
    }

    getOrderOfStore(): Observable<any> {
        return this.api.get(Order).pipe(
            map(respone => {
                if(respone.code === 200)
                {
                    // console.log(respone)
                    return  respone.data
                }
            }), catchError(value => throwError(value))
        );
    }

    deleteOrder(){
        return this.api.delete(Order);
    }

    updateOrderAfterPay(formUpdate){
        return this.api.put( Order, formUpdate)
    }

    updateOrderByPoint(formUpdate){
        return this.api.put( PaymentOrderByPoint, formUpdate)
    }

    updateOrderCustomerInfoAfterScan(phone , order_id){
        let param = new HttpParams();
        param = param.append('phone', phone);
        param = param.append('id', order_id);
        if(this.api.isEnable()){
            return this.http.put<any>(UpdateOrderInfor ,'' ,{headers : this.api.headers, params : param})
        }
    }

    
    updateOrderSuccess(uuid){
        let param = new HttpParams();
        param = param.append('uuid', uuid);
        if(this.api.isEnable()){
            return this.http.put<any>(updateOrderSuccess ,'' ,{headers : this.api.headers, params : param})
        }
    }

    getDataPayment(uuid){
        let param = new HttpParams();
        param = param.append('uuid', uuid);
        if(this.api.isEnable()){
            return this.http.get<any>(dataPaymentOrder ,{headers : this.api.headers, params : param})
        }
    }

    updateOrderAfterScanVoucher(uuid , brand_id , value){
        let param = new HttpParams();
        param = param.append('uuid', uuid);
        param = param.append('brand_id', brand_id);
        param = param.append('value', value);
        if(this.api.isEnable()){
            return this.http.put<any>(scanVoucher ,'' ,{headers : this.api.headers, params : param})
        }
    }

    getorderByUuid(uuid){
        let param = new HttpParams();
        param = param.append('uuid', uuid);
        if(this.api.isEnable()){
            return this.http.get<any>(getOrderByUuid ,{headers : this.api.headers, params : param}).pipe(
                map(data=>{
                    // console.log(data)
                    let item = this.renderDataOrderDetail(data.data)
                    let dataOrder = (!CheckNullOrUndefinedOrEmpty(data.data)) ? data.data : null;
                    return {item: item , dataOrder : dataOrder}
                })
            )

        }
    }

    getOrderDetail(): Observable<any> {
        return this.api.get(Order).pipe(
            map(respone => {
                if(respone.code === 200)
                {
                    let order_id;
                    let order_uuid;
                    if(!CheckNullOrUndefinedOrEmpty(respone.data)){
                        order_id = respone.data.id;
                        order_uuid = respone.data.uuid;
                    }
                    return  {item: this.renderDataOrderDetail(respone.data) , order_id: order_id, order_uuid: order_uuid}
                }
            }), catchError(value => throwError(value))
        );
    }

    deleteOrderitem(product_id): Observable<any>{
        let param = new HttpParams();
        param = param.append('product_id', product_id);
        return this.http.delete<any>(OrderLineItem , {headers : this.api.headers, params : param})
    }
    
    renderDataOrderDetail(data){
         let listProduct = [];
        if(!CheckNullOrUndefinedOrEmpty(data) && !CheckNullOrUndefinedOrEmpty(data.orderLineItem)){
            data.orderLineItem.forEach(element => {
                let item = new OrderDetail();
                if(!CheckNullOrUndefinedOrEmpty(element.product)){
                    item.cover_photo_key = element.product.product_image;
                    item.product_name = element.product.product_name;
                    item.price = Number(element.product.price)
                    item.properties = element.product.properties;
                    item.priceFormat = formatCurrency(element.product.price,"en-US",
                    "VND ",
                    "code",
                    "0.0-2" )
                }
                item.quantity = element.quantity;
                item.disableDecrease = (element.quantity > 0)? false : true;
                item.id = element.product_id;
                listProduct.push(item);
            });

        }
        return listProduct;
    }

}