import { Component, OnInit, Input, Output , ViewEncapsulation, ChangeDetectorRef, NgZone} from '@angular/core';
import { Router } from '@angular/router';
import { LangChangeEvent, TranslateService } from '@ngx-translate/core';
import { GridApi, GridOptions, GridSizeChangedEvent, IGetRowsParams } from 'ag-grid-community';

import { Title } from 'app/core/enum/title';
import { BranchService } from 'app/core/service/branch.service';
import { CheckNullOrUndefinedOrEmpty } from 'app/core/utils/common-function';

@Component({
  selector: 'app-store-account',
  templateUrl: './store-account.component.html',
  styleUrls: ['./store-account.component.scss']
})
export class StoreAccountComponent implements OnInit {

  title = Title.DOT;
  accountList = []
  defaultColDef
  gridApi: GridApi
  gridOptions:GridOptions;
  gridColumnApi;
  page: number = 1;
  totalElements: number;
  noRowsTemplate;
  loadingTemplate;
  columnAdvisor;
  public currentLang: string;
  cruitCustomer: string;
  cruitStatus: string;

  constructor(
    private branchService: BranchService,
    public translateService: TranslateService,
    private _changeDetectorRef: ChangeDetectorRef,
    private router : Router,
    private ngZone: NgZone,

    ) {

      this.currentLang = translateService.currentLang;

      this.translateService.onLangChange.subscribe((event: LangChangeEvent) => {
        this.currentLang = event.lang;
        this._changeDetectorRef.detectChanges();
        this.cruitCustomer = this.translateService.instant('MENU_BRANCH.ACCOUNT.ACCOUNT');
        this.cruitStatus = this.translateService.instant('MENU_BRANCH.ACCOUNT.STATUS');
        var colCustomer = this.gridOptions.columnApi.getColumn('customer');
        // var colDefCustomer = colCustomer.getColDef();
        // colDefCustomer.headerName = this.cruitCustomer;
        var colStatus = this.gridOptions.columnApi.getColumn('status');
        var colDefStatus = colStatus.getColDef();
        colDefStatus.headerName = this.cruitStatus;
        this.gridOptions.api.refreshHeader();
      });

      this.gridOptions = {
        rowHeight: 65,
        rowStyle: { 'padding': '0px 0px' },
        rowSelection: 'single',
        maxBlocksInCache: 2,
        cacheBlockSize: 10,
        paginationAutoPageSize: false,
        pagination: true,
        paginationPageSize: 10,
        enableServerSideFilter: true,
        enableFilter:true,
        enableServerSideSorting: true,
        rowModelType: 'infinite',
        sortingOrder: ["asc", "desc"],
      };
      this.loadingTemplate =
      `<span class="ag-overlay-loading-center">data is loading...</span>`;
      this.noRowsTemplate = `"<span">no rows to show</span>"`;
  }

  
  ngOnInit(): void {
    this.columnAdvisor = [
      { headerName: this.cruitCustomer, field: "account", resizable: false, filter: 'agTextColumnFilter', cellRenderer: CustomerRenderer},
      { headerName: this.cruitStatus, field: "status", resizable: false, filter: true,
      filterParams: {
        filterOptions: ['inRange'],
      }, cellRenderer: StatusRenderer},
    ];
  
  }

  onGridReady(params) {
    this.gridApi = params.api
    this.getMethod(params.api)
  }

  onGridSizeChanged(params: GridSizeChangedEvent)
  {
    params.api.sizeColumnsToFit();
  }

  getMethod(api:GridApi) {
  
    let datasource = {
      getRows: (params: IGetRowsParams) => {
       
        this.page = params.startRow/10 + 1
        this.branchService.getListAccount(params).subscribe(data => {
            // console.log("THACH")
            // console.log("THACH",data)
            // this.accountList = data.data;
            this.accountList = []
            if(this.accountList.length){
              this.gridOptions.api.showNoRowsOverlay();
            }
            this.totalElements = 100
            setTimeout(function(){
              params.successCallback(data.data , data.count)
            },500)
          },err=>[]);
        
      }
    };
    // this.gridOptions.api.setDatasource(datasource);
    api.setDatasource(datasource); 
    
  }

  onViewCustomerNaepInfor(event){
    this.ngZone.run(() => this.router.navigate(['advisor/recruitment/customer-naep-infomation'],{ queryParams: { uuid: event.data.customer.uuid} })).then();
  }

}

function CustomerRenderer(params)
{
  if(!CheckNullOrUndefinedOrEmpty(params.value)){
    console.log(params.value,"AAA")
    let customer_name = params.value.name;
    let customer_email = params.value.email;
    let customer_phone_number = params.value.phone_number;
    let returnData = `
    <p class="p-aggrid" style="margin:0px; padding:0px; color: var(--border-color); font-size: 12px" >${customer_name}</p>
    <p style="margin: -5px 0px 0px 0px;; padding:0px; color: var(--border-color); font-size: 12px">${customer_email}</p>
    <p style="margin: -10px 0px 0px 0px;; padding:0px; color: var(--border-color); font-size: 12px">${customer_phone_number}</p>
    `
    return returnData;
  }
  
  
}

function StatusRenderer(params)
{
  
  if(!CheckNullOrUndefinedOrEmpty(params.value)){
    let returnData : string;
    const manager_at = params.value.manager_at;
    if (manager_at === null)
    {
      returnData = `<p style="color: var(--primary-color); font-size: 14px; margin:0px; padding:0px; font-size: 12px; white-space: normal;">Manager At : None</p>`;
    }else{
      returnData = `<p style="color: var(--text-color); font-size: 14px; margin:0px; padding:0px; font-size: 12px; white-space: normal;">Manager At : ${manager_at}</p>`;
    }
    return returnData
  }
  
}