import { Component, NgZone ,OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { Title } from 'app/core/enum/title';
import { ActionOrder, FormOrder } from 'app/core/models/bk-point/order-bkpoint.model';
import { SharedService } from 'app/core/service/commom/shared.service';
import { OrderService } from 'app/core/service/order.service';
import { CommonDialogComponent } from 'app/main/common-dialog/common-dialog.component';
import { environment } from 'environments/environment';

@Component({
  selector: 'app-shopping-bag',
  templateUrl: './shopping-bag.component.html',
  styleUrls: ['./shopping-bag.component.scss']
})
export class ShoppingBagComponent implements OnInit {

  constructor(
    private orderService : OrderService,
    private sharedService: SharedService,
    private ngZone: NgZone,
    private router : Router,
    public dialog: MatDialog
  ) { }

  title = Title.DOT;
  storeUrl = environment.storageUrl;
  carArr = []
  loading: boolean = false;
  subTotal: number = 0;
  totalPrice: number = 0;
  disableDecreaseButton: boolean = false;
  disableButton: boolean = false;
  total_product: number = 0;
  orderId: number;
  uuid: string;
  ngOnInit(): void {
    this.orderService.getOrderDetail().subscribe(data=>{
      this.carArr = data.item;
      this.orderId = data.order_id;
      this.uuid = data.order_uuid;
    });

    this.orderService.getOrderOfStore().subscribe(data=>{
      this.totalPrice = data.total_price;  
      this.subTotal = data.total_price;  
    })
  }

  nextToSelectPayment(){
    this.ngZone.run(() => this.router.navigate(['bk-point/select-payment'],{ queryParams: { uuid: this.uuid} })).then();
  }

  scanQRcode(){
    this.ngZone.run(() => this.router.navigate(['bk-point/scan-bar-code'],{ queryParams: { id: this.orderId , uuid: this.uuid} })).then();
  }

  removeAll(){
    this.orderService.deleteOrder().subscribe(data=>{
      // console.log(data)
      if(data.code === 200){
        this.subTotal = 0;
        this.totalPrice = 0
        this.orderService.getOrderDetail().subscribe(data=>{
          this.carArr = data.item
        })
        this.sharedService.sharedMessage.subscribe(
          (message) => (this.total_product = message)
        );
        this.sharedService.nextCart(this.total_product - this.total_product);
      }
    })
  }

  removeProductOrder(product_id , quantity){
    this.orderService.deleteOrderitem(product_id).subscribe(data=>{
      // console.log(data)
      if(data.code === 200){
        this.orderService.getOrderDetail().subscribe(data=>{
          this.carArr = data
        })
        this.sharedService.sharedMessage.subscribe(
          (message) => (this.total_product = message)
        );
        this.sharedService.nextCart(this.total_product - quantity);
      }
    })
  }

  addOrRemoveProductInOrder(productId , action , productPrice){
    this.disableDecreaseButton = true;
    this.disableButton = true;
    this.carArr.forEach(element=>{
      if(element.id == productId){
        if(action === ActionOrder.ADD){
          element.quantity +=1;
        }else if(action === ActionOrder.SUB){
          if(element.quantity==0){
            return;
          }else if(element.quantity == 1){
            element.disableDecrease = true;
            element.quantity -=1;
          }else{
            element.quantity -=1;
          }
        }
      }
    })
    let formOrder = new FormOrder();
    if(action === ActionOrder.ADD){
      this.totalPrice += Number(productPrice);
      formOrder.total_price = Number(this.totalPrice);
      formOrder.action = ActionOrder.ADD;
      formOrder.product_id = productId;
      this.orderService.createOrder(formOrder).subscribe(data=>{
        this.disableDecreaseButton = false;
        this.disableButton = false;
        if(data.code === 200){
          this.sharedService.sharedMessage.subscribe(
            (message) => (this.total_product = message)
          );
          this.sharedService.nextCart(this.total_product + 1);
        }
        // console.log(data)
      })
    }else if(action === ActionOrder.SUB){
      this.totalPrice -= Number(productPrice);
      formOrder.total_price = this.totalPrice;
      formOrder.action = ActionOrder.SUB;
      formOrder.product_id = productId;
      this.orderService.createOrder(formOrder).subscribe(data=>{
        console.log(data)
        this.disableDecreaseButton = false;
        this.disableButton = false;
        if(data.code === 200){
          this.sharedService.sharedMessage.subscribe(
            (message) => (this.total_product = message)
          );
          this.sharedService.nextCart(this.total_product - 1);
        }else if(data.code === 202){
          const dialog = this.dialog.open(CommonDialogComponent, {
            width: "500px",
            data: {
              message: "Không nên trừ âm.",
              title:
                "NOTIFICATION",
              colorButton: true
            },
          });
          dialog.afterClosed().subscribe(()=>{
            console.log('To be continue.')
            // this.router.navigate(['/store/store-detail'])
          })
        }
        // console.log(data)
      })
    }

  }

}
