export class FormStore{
    store_name: string;
    is_active : boolean;
    store_description : string;
    city : string;
    district : string;
    detail_address : string;
    manager: Array<ManagerOfStore>=[];
    store_photo_key: string;
}

export class ManagerOfStore{
    store_manager_name: string;
    store_manager_phone: string;
    store_manager_email: string;
}

export class StoreInformation{
    name: string;
    address: string;
    createdAt: string;
    isActive: boolean;
    description: string;
    uuid: string;
    image: string;
    listManager: string[] = [];
}