import { Component, OnInit } from '@angular/core';
import { Location, formatDate } from '@angular/common';
import { Title } from 'app/core/enum/title';
import { BranchService } from 'app/core/service/branch.service';
import { CheckNullOrUndefinedOrEmpty } from 'app/core/utils/common-function';
import { CommonDialogComponent } from 'app/main/common-dialog/common-dialog.component';
import { MatDialog } from '@angular/material/dialog';
import { ConfigBrand } from 'app/core/models/bk-point/config-brand.model';
import { Router } from '@angular/router';
import { RANKEDTYPE } from 'app/core/enum/ranked-type.enum';

@Component({
  selector: 'app-config-brand',
  templateUrl: './config-brand.component.html',
  styleUrls: ['./config-brand.component.scss']
})
export class ConfigBrandComponent implements OnInit {

  title = Title.LEFT;
  buttonName: string = 'CREATE';
  active: boolean = false;

  //Rank
  silver: number;
  gold: number;
  diamond: number;

  //point
  accumulate: number;
  payPoint: number;
  
  //check
  checkCreate: boolean = false;
  submitted: boolean = false;
  constructor(
    private location : Location,
    private branchService: BranchService,
    public dialog: MatDialog,
    private router: Router,
  ) { }

  ngOnInit(): void {
    this.branchService.getBrandConfigDetail().subscribe(data=>{
      if(CheckNullOrUndefinedOrEmpty(data.ranked)){
        this.checkCreate = true;
      }else{
        this.buttonName = 'UPDATE';
        this.accumulate = data.return_percent;
        this.payPoint = data.point_to_money;
        data.ranked.forEach(element => {
          if(element.ranked_name === RANKEDTYPE.SILVER){
            this.silver = Number(element.reached_at);
          }else if(element.ranked_name === RANKEDTYPE.GOLD){
            this.gold = Number(element.reached_at);
          }else if(element.ranked_name === RANKEDTYPE.DIAMOND){
            this.diamond = Number(element.reached_at);
          }
        });
      }
    })
  }

  back() {
    this.location.back();
  }

  createConfig(){
    this.submitted = true;
    if(CheckNullOrUndefinedOrEmpty(this.silver) ||
      CheckNullOrUndefinedOrEmpty(this.gold) ||
      CheckNullOrUndefinedOrEmpty(this.diamond) ||
      CheckNullOrUndefinedOrEmpty(this.accumulate) ||
      CheckNullOrUndefinedOrEmpty(this.payPoint)
    ){
      return;
    }else{
      
      const dialogNotifi = this.dialog.open(CommonDialogComponent, {
        width: "500px",
        data: {
          message: this.checkCreate ? "Are you sure you want to create config?" : "Are you sure you want to update config?",
          title:
            "NOTIFICATION",
          colorButton: false
        },
      });

      dialogNotifi.afterClosed().subscribe((result)=>{
        if(result === true){
          let newConfigBrand = new ConfigBrand();
          newConfigBrand.silver = Number(this.silver);
          newConfigBrand.gold = Number(this.gold);
          newConfigBrand.diamond = Number(this.diamond);
          newConfigBrand.return_percent = Number(this.accumulate);
          newConfigBrand.point_to_money =Number(this.payPoint);

          this.branchService.updateConfigBrand(newConfigBrand).subscribe(data=>{
            this.buttonName = 'Processing...';
            this.active = true;
            if(data.code === 200){
              this.buttonName = 'CREATE';
              this.active = false;
              const dialog = this.dialog.open(CommonDialogComponent, {
                width: "500px",
                data: {
                  message: this.checkCreate ? "Create config successfully." : "Update config successfully.",
                  title:
                    "NOTIFICATION",
                  colorButton: false
                },
              });

              dialog.afterClosed().subscribe(()=>{
                this.router.navigate(['/bk-point/config-detail'])
              })
            }
          })
        }
      });

    }
  }

}
