import { Component, NgZone, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { Title } from 'app/core/enum/title';
import { StoreInformation } from 'app/core/models/bk-point/store.model';
import { BranchService } from 'app/core/service/branch.service';
import { Location } from '@angular/common'
import { environment } from 'environments/environment';

@Component({
  selector: 'app-store-information',
  templateUrl: './store-information.component.html',
  styleUrls: ['./store-information.component.scss']
})
export class StoreInformationComponent implements OnInit {

  constructor(
    private branchService: BranchService,
    private _location : Location,
    public dialog: MatDialog, private _activatedRoute: ActivatedRoute,
    private ngZone: NgZone,
    private router : Router,
  ) { }
  title = Title.LEFT;
  storeInfor : StoreInformation;
  uuid: string;
  storageUrl = environment.storageUrl;
  imgStore: string;
  ngOnInit(): void {
    this._activatedRoute.queryParams.subscribe(param=>{
      this.uuid = param.uuid;
      // console.log(this.uuid)
    })
    this.branchService.getStoreDetail(this.uuid).subscribe(data=>{
      this.storeInfor = data;
      this.imgStore = this.storageUrl + data.image;
    })
  }

  editStore(){
    this.ngZone.run(() => this.router.navigate(['store/create-store'],{ queryParams: { uuid: this.uuid} })).then();
  }

  cancleInfo(){
    this.ngZone.run(() => this.router.navigate(['store'])).then();
  }
}
