import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Mode } from 'app/core/enum/mode';
import { Title } from 'app/core/enum/title';
import { CategoryDetail } from 'app/core/models/bk-point/category-bkpoint.model';
import { BkPointCategoryService } from 'app/core/service/category-bkpoint.service'
import { CheckNullOrUndefinedOrEmpty } from 'app/core/utils/common-function';
@Component({
  selector: 'app-category',
  templateUrl: './category.component.html',
  styleUrls: ['./category.component.scss']
})
export class CategoryComponent implements OnInit {

  constructor(
    private bkPointCategoryService  : BkPointCategoryService,
    private activatedRoute : ActivatedRoute
  ) { }
  title = Title.DOT;
  listCategory: Array<CategoryDetail>;

  ngOnInit(): void {

    this.bkPointCategoryService.getListCategoryByBrand().subscribe(data=>{
      this.listCategory = data;
    })
  }



}
