import { Component, NgZone, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Title } from 'app/core/enum/title';
import { OrderService } from 'app/core/service/order.service';

@Component({
  selector: 'app-select-payment',
  templateUrl: './select-payment.component.html',
  styleUrls: ['./select-payment.component.scss']
})
export class SelectPaymentComponent implements OnInit {

  constructor(
    private router: Router,
    private activedRoute: ActivatedRoute,
    private ngZone: NgZone,
    private orderService: OrderService
  ) { }
  uuid: string;
  loading: boolean = false;
  title = Title.LEFT_LINK;
  orderId: number;
  subtotal: number;
  total: number;
  discount: number = 0;
  ngOnInit(): void {
    this.activedRoute.queryParams.subscribe((param) => {
      this.uuid = param.uuid;
    });
    this.orderService.getDataPayment(this.uuid).subscribe(data=>{
      // console.log(data)
      if(data.code === 200){
        this.orderId = data.data.id;
        this.subtotal = data.data.total_price;
        this.total = data.data.total_price;
      }
    })
  }

  scanQRcode(){
    this.ngZone.run(() => this.router.navigate(['bk-point/scan-bar-code'],{ queryParams: {mode: 'sc' , id: this.orderId , uuid: this.uuid} })).then();
  }

  useVoucher(){
    this.ngZone.run(() => this.router.navigate(['bk-point/scan-bar-code'],{ queryParams: {mode: 'dc' , id: this.orderId , uuid: this.uuid} })).then();
  }

  customerFree(){
    this.ngZone.run(() => this.router.navigate(['bk-point/order-summary'],{ queryParams: { uuid: this.uuid} })).then();
  }
}
