import { BranchService } from 'app/core/service/branch.service';

import { Title } from 'app/core/enum/title';
import { Component, OnInit, Input, Output , ViewEncapsulation, ChangeDetectorRef, NgZone} from '@angular/core';
import { environment } from 'environments/environment';
import { Subject, Observable } from 'rxjs';
import { debounceTime, distinctUntilChanged, switchMap } from 'rxjs/operators';
import { LangChangeEvent, TranslateService } from '@ngx-translate/core';
import { element } from 'protractor';
import { Router } from '@angular/router';
import { GridApi, GridSizeChangedEvent, IGetRowsParams, GridOptions } from 'ag-grid-community';
import { CheckNullOrUndefinedOrEmpty } from 'app/core/utils/common-function';


@Component({
  selector: 'store',
  templateUrl: './store.component.html',
  styleUrls: ['./store.component.scss'],
  encapsulation: ViewEncapsulation.Emulated
})
export class StoreComponent implements OnInit {
  title = Title.DOT;
  advisorArray = []
  defaultColDef
  gridApi: GridApi
  gridOptions:GridOptions;
  gridColumnApi;
  page: number = 1;
  totalElements: number;
  noRowsTemplate;
  loadingTemplate;
  columnAdvisor;
  public currentLang: string;
  cruitCustomer: string;
  cruitStatus: string;

  constructor(private branchService: BranchService,
    public translateService: TranslateService,
    private _changeDetectorRef: ChangeDetectorRef,
    private router : Router,
    private ngZone: NgZone
    ) {

      this.currentLang = translateService.currentLang;

      this.translateService.onLangChange.subscribe((event: LangChangeEvent) => {
        this.currentLang = event.lang;
        this._changeDetectorRef.detectChanges();
        this.cruitCustomer = this.translateService.instant('MENU_BRANCH.STORE.STORE');
        this.cruitStatus = this.translateService.instant('MENU_BRANCH.STORE.INFOR');
        let colCustomer = this.gridOptions.columnApi.getColumn('customer');
        // let colDefCustomer = colCustomer.getColDef();
        // colDefCustomer.headerName = this.cruitCustomer;
        let colStatus = this.gridOptions.columnApi.getColumn('status');
        // let colDefStatus = colStatus.getColDef();
        // colDefStatus.headerName = this.cruitStatus;
        this.gridOptions.api.refreshHeader();
      });

      this.gridOptions = {
        rowHeight: 65,
        rowStyle: { 'padding': '0px 0px' },
        rowSelection: 'single',
        maxBlocksInCache: 2,
        cacheBlockSize: 10,
        paginationAutoPageSize: false,
        pagination: true,
        paginationPageSize: 10,
        enableServerSideFilter: true,
        enableFilter:true,
        enableServerSideSorting: true,
        rowModelType: 'infinite',
        sortingOrder: ["asc", "desc"],
      };
      this.loadingTemplate =
      `<span class="ag-overlay-loading-center">data is loading...</span>`;
      this.noRowsTemplate = `"<span">no rows to show</span>"`;
  }

  
  ngOnInit(): void {
    this.columnAdvisor = [
      { headerName: this.cruitCustomer, field: "store", resizable: false, filter: 'agTextColumnFilter', cellRenderer: StoreRenderer},
      { headerName: this.cruitStatus, field: "information", resizable: false, filter: true,
      filterParams: {
        filterOptions: ['inRange'],
      }, cellRenderer: InformationRenderer},
    ];
  
  }

  onGridReady(params) {
    this.gridApi = params.api
    this.getMethod(params.api)
  }

  onGridSizeChanged(params: GridSizeChangedEvent)
  {
    params.api.sizeColumnsToFit();
  }

  getMethod(api:GridApi) {
  
    let datasource = {
      getRows: (params: IGetRowsParams) => {
       
        this.page = params.startRow/10 + 1
          this.branchService.getListStore(params).subscribe(data => {
            // console.log(data)
            this.advisorArray = data.data;
            // this.advisorArray = []
            // if(this.advisorArray.length){
            //   this.gridOptions.api.showNoRowsOverlay();
            // }
            this.totalElements = data.count;
            setTimeout(function(){
              params.successCallback(data.data , data.count)
            },500)
          },err=>[]);
        
      }
    };
   
    api.setDatasource(datasource); 
    
  }

  onViewCustomerNaepInfor(event){
    this.ngZone.run(() => this.router.navigate(['store/store-information'],{ queryParams: { uuid: event.data.uuid} })).then();
  }

}

function StoreRenderer(params)
{
  if(!CheckNullOrUndefinedOrEmpty(params.value)){
    let name = params.value.name;
    let create_at = params.value.create_at;
    
    let returnData = `
    <p class="p-aggrid" style="margin:0px; padding:0px; color: var(--border-color); font-size: 12px" >Name: ${name}</p>
    <p style="margin: -5px 0px 0px 0px;; padding:0px; color: var(--border-color); font-size: 12px">Created At: ${create_at}</p>
    `
    return returnData;
  }
  
  
}

function InformationRenderer(params)
{
  if(!CheckNullOrUndefinedOrEmpty(params.value)){
    
    let manager_by = params.value.manager_by;
    let address = params.value.address;
    // <p class="p-aggrid" style="margin:0px; padding:0px; color: var(--border-color); font-size: 12px ; white-space: normal;" >Manager By: ${manager_by}</p>
    let returnData = `
    <p style="margin: -5px 0px 0px 0px;; padding:0px; color: var(--border-color); font-size: 12px"; white-space: normal;>Address: ${address}</p>
    `
    return returnData;
    
  }
  
}