import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { Location } from '@angular/common';
import { Title } from 'app/core/enum/title';
import { environment } from 'environments/environment';
import { CommonDialogComponent } from 'app/main/common-dialog/common-dialog.component';
import { Mode } from 'app/core/enum/mode';
import { CreateVoucher } from 'app/core/models/bk-point/voucher-bkpoint.model';
import { CheckNullOrUndefinedOrEmpty } from 'app/core/utils/common-function';
import { VoucherService } from 'app/core/service/voucher.service';
import { BkPointCategoryService } from 'app/core/service/category-bkpoint.service';

@Component({
  selector: 'app-promotion-configura-tion',
  templateUrl: './promotion-configuration.component.html',
  styleUrls: ['./promotion-configuration.component.scss']
})
export class PromotionConfiguraTIONComponent implements OnInit {

  constructor(
    private activatedRoute : ActivatedRoute,
    private router: Router,
    private location : Location,
    public dialog: MatDialog,
    private voucherService: VoucherService,
    private bkPointCategoryService : BkPointCategoryService,
  ) { }

  title = Title.LEFT;

  storageUrl = environment.storageUrl;

  maximumImageSize : number = environment.imageSize;
  buttonName: string = "CREATE";
  //Variable 
  name: string;
  description: string
  activeOnlineStore: boolean =false;

  myProfileImgUrl: string
  mode: Mode;
  discount: number;
  min_order_value: number;
  max_value_discount: number;
  voucherStartTimeData: Date;
  voucherEndTimeData: Date;
  redemption_points: number;  
  minDateStart = new Date();
  coverphotokey: string;
  voucher_id: number;
  submitted: boolean = false
  ngOnInit(): void {
    this.activatedRoute.queryParams.subscribe(param=>{
      this.voucher_id = param.id
      this.chechMode(this.voucher_id);
    })

    if(this.mode === 'UPDATE'){
      this.voucherService.getVoucherDetailByBrand(this.voucher_id).subscribe(data=>{
        // console.log(data)
        this.name = data.voucher_name;
        this.description = data.description;
        this.discount = data.percent_discount;
        this.max_value_discount = data.max_redeem_value;
        this.min_order_value = data.min_order_value;
        this.activeOnlineStore = data.is_active;
        this.myProfileImgUrl = this.storageUrl + data.voucher_photo;
        this.voucherStartTimeData = new Date(data.start_time);
        this.voucherEndTimeData = new Date(data.end_time);
        this.redemption_points = data.point_to_redeem;
        this.coverphotokey = data.voucher_photo
      })
    }
  }

  chechMode(id){
    if(CheckNullOrUndefinedOrEmpty(id)){
      this.mode = Mode.CREATE;
    }else{
      this.mode = Mode.UPDATE;
    }
  }

  createVoucher(){
    this.submitted = true;
    if(CheckNullOrUndefinedOrEmpty(this.name) || CheckNullOrUndefinedOrEmpty(this.description)||
    CheckNullOrUndefinedOrEmpty(this.discount) || CheckNullOrUndefinedOrEmpty(this.min_order_value)||
    CheckNullOrUndefinedOrEmpty(this.max_value_discount) || CheckNullOrUndefinedOrEmpty(this.voucherStartTimeData)||
    CheckNullOrUndefinedOrEmpty(this.voucherEndTimeData) || CheckNullOrUndefinedOrEmpty(this.redemption_points)
    ){
      return;
    }else{
      const dialogNotifi = this.dialog.open(CommonDialogComponent, {
        width: "500px",
        data: {
          message: "Are you sure you want to create voucher?",
          title:
            "NOTIFICATION",
          colorButton: false
        },
      });
      dialogNotifi.afterClosed().subscribe((result)=>{
        if(result === true){
          let createVoucher = new CreateVoucher();
          createVoucher.voucher_name = this.name;
          createVoucher.description = this.description;
          createVoucher.min_order_value = this.min_order_value;
          createVoucher.max_redeem_value = this.max_value_discount;
          createVoucher.percent_discount = this.discount;
          createVoucher.start_time = this.voucherStartTimeData;
          createVoucher.end_time = this.voucherEndTimeData;
          createVoucher.is_active = (this.activeOnlineStore && this.checkActive) ? this.activeOnlineStore : false;
          createVoucher.point_to_redeem = this.redemption_points;
          createVoucher.voucher_photo = this.coverphotokey;

          // console.log(createVoucher);
          this.voucherService.createVoucherByBrand(createVoucher).subscribe(data=>{
            if(data.code === 200){
              const dialog = this.dialog.open(CommonDialogComponent, {
                width: "500px",
                data: {
                  message: "Create voucher successfully.",
                  title:
                    "NOTIFICATION",
                  colorButton: false
                },
              });
              dialog.afterClosed().subscribe(()=>{
                this.router.navigate(['/bk-point/list-promotion'])
              })
            }
          })

        }})
    }
  }

  checkActive: boolean = false;
  newDate = new Date();
  changeStart(event){
    let date = event.getDate()+'-'+(event.getMonth()+1)+'-'+event.getFullYear();
    let today = this.newDate.getDate()+'-'+(this.newDate.getMonth()+1)+'-'+this.newDate.getFullYear();
    if(date == today){
      this.checkActive = true;
    }
  }

  updateVoucher(){
    this.submitted = true;
    if(CheckNullOrUndefinedOrEmpty(this.name) || CheckNullOrUndefinedOrEmpty(this.description)||
    CheckNullOrUndefinedOrEmpty(this.discount) || CheckNullOrUndefinedOrEmpty(this.min_order_value)||
    CheckNullOrUndefinedOrEmpty(this.max_value_discount) || CheckNullOrUndefinedOrEmpty(this.voucherStartTimeData)||
    CheckNullOrUndefinedOrEmpty(this.voucherEndTimeData) || CheckNullOrUndefinedOrEmpty(this.redemption_points)
    ){
      return;
    }else{
      const dialogNotifi = this.dialog.open(CommonDialogComponent, {
        width: "500px",
        data: {
          message: "Are you sure you want to update this voucher?",
          title:
            "NOTIFICATION",
          colorButton: false
        },
      });
      dialogNotifi.afterClosed().subscribe((result)=>{
        if(result === true){
          let createVoucher = new CreateVoucher();
          createVoucher.voucher_name = this.name;
          createVoucher.description = this.description;
          createVoucher.min_order_value = this.min_order_value;
          createVoucher.max_redeem_value = this.max_value_discount;
          createVoucher.percent_discount = this.discount;
          createVoucher.start_time = this.voucherStartTimeData;
          createVoucher.end_time = this.voucherEndTimeData;
          createVoucher.is_active = (this.activeOnlineStore && this.checkActive) ? this.activeOnlineStore : false;
          createVoucher.point_to_redeem = this.redemption_points;
          createVoucher.voucher_photo = this.coverphotokey;
          // console.log(createVoucher);
          this.voucherService.updateVoucherDetail(this.voucher_id , createVoucher).subscribe(data=>{
            if(data.code === 200){
              const dialog = this.dialog.open(CommonDialogComponent, {
                width: "500px",
                data: {
                  message: "Update voucher successfully.",
                  title:
                    "NOTIFICATION",
                  colorButton: false
                },
              });
              dialog.afterClosed().subscribe(()=>{
                this.router.navigate(['/bk-point/list-promotion'])
              })
            }
          })
        }
      })
    }
  }

  back(){
    this.location.back();
  }

  onSelectFile(event): void {
   
    if (event.target.files && event.target.files[0]) {
      const file = event.target.files[0];
      if (file.type != "image/bmp" && file.type != "image/jpeg" && file.type != "image/jpg" && file.type != "image/png") {
        return;
      }
      if (file.size > this.maximumImageSize * 1024 * 1024) {
        const dialogNotifi = this.dialog.open(CommonDialogComponent, {
          width: "500px",
          data: {
            message:
              `Maximum File Size ${this.maximumImageSize} MB. Please choose another picture`,
            title:
              "NOTIFICATION",
            colorButton: false
          },
        });
        dialogNotifi.afterClosed().subscribe(data => {
  
        });
        return;
      }
      const reader = new FileReader();
      reader.readAsDataURL(event.target.files[0]);
      reader.onload = (event) => {
        let preSignedUrl: string;
        let voucherImage: string;
        this.bkPointCategoryService.getPreSignedUrl(Date.now().toString() + file.name, file.type).subscribe(response => {
          if (response.code === 200) {
            console.log(response)
            voucherImage = response.key;
            preSignedUrl = response.url;
            this.bkPointCategoryService.uploadImage(preSignedUrl, file.type, file).subscribe(
              response => {
                this.myProfileImgUrl = this.storageUrl + voucherImage;
              }
            );
            this.coverphotokey = voucherImage;
            return voucherImage;
          }
        });
      };
    }
  }

}
