import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Title } from 'app/core/enum/title';
import { ActionOrder, FormOrder } from 'app/core/models/bk-point/order-bkpoint.model';
import { SharedService } from 'app/core/service/commom/shared.service';
import { OrderService } from 'app/core/service/order.service';
import { BkPointProductService } from 'app/core/service/product-bkpoint.service';
import { CheckNullOrUndefinedOrEmpty } from 'app/core/utils/common-function';
import { nextTick } from 'process';

@Component({
  selector: 'app-list-product-bkpoint',
  templateUrl: './list-product-bkpoint.component.html',
  styleUrls: ['./list-product-bkpoint.component.scss']
})
export class ListProductBkpointComponent implements OnInit {

  constructor(
    public activatedRoute: ActivatedRoute,
    private bkPointProductService: BkPointProductService,
    private orderService : OrderService,
    private sharedService: SharedService,
  ) { }
  title = Title.LEFT;
  listProduct = [];
  categoryId: number
  checkLength: boolean;
  disableDecreaseButton: boolean = false;
  disableButton: boolean = false;

  totalPrice: number = 0;
  total_product : number = 0;

  ngOnInit(): void {
    this.activatedRoute.queryParams
    .subscribe(params=>{
      this.categoryId = params.id;
    }) 

    // this.sharedService.sharedPrice.subscribe(
    //   (valuePrice) => (this.totalPrice = valuePrice)
    // );

    this.bkPointProductService.getListProductByCategoty(this.categoryId , '').subscribe( data => {
      this.listProduct = data;
      // console.log(this.listProduct)
      setTimeout(() => {
        this.checkLength = this.listProduct.length > 0 ? false : true; 
        this.setDataQuantity();
      }, 500);
    });
  }

  search(searchString: string):void{
    setTimeout(() => {
      this.bkPointProductService.getListProductByCategoty(this.categoryId , searchString).subscribe( data => {
        this.listProduct = data;
      })
    }, 500);
  }

  setDataQuantity(){
    this.orderService.getOrderOfStore().subscribe(data=>{
      if(!CheckNullOrUndefinedOrEmpty(data) && data.orderLineItem.length > 0){
        data.orderLineItem.forEach(element => {
          this.listProduct.forEach(e=>{
            if(e.id == element.product_id){
              e.quantity = element.quantity;
              e.disableDecrease = false;
              // nextTick;
            }
          })
        });
      }
    })
  }

  addOrRemoveProductInOrder(productId , action , productPrice){
    this.disableDecreaseButton = true;
    this.disableButton = true;
    this.listProduct.forEach(element=>{
      if(element.id == productId){
        if(action === ActionOrder.ADD){
          element.quantity +=1;
          element.disableDecrease = false;
        }else if(action === ActionOrder.SUB){
          if(element.quantity==0){
            return;
          }else if(element.quantity == 1){
            element.disableDecrease = true;
            element.quantity -=1;
          }else{
            element.quantity -=1;
          }
        }
      }
    })
    let formOrder = new FormOrder();
    if(action === ActionOrder.ADD){
      this.totalPrice += Number(productPrice);
      formOrder.total_price = this.totalPrice;
      formOrder.action = ActionOrder.ADD;
      formOrder.product_id = productId;
      this.orderService.createOrder(formOrder).subscribe(data=>{
        this.disableDecreaseButton = false;
        this.disableButton = false;
        if(data.code === 200){
          this.sharedService.sharedMessage.subscribe(
            (message) => (this.total_product = message)
          );
          this.sharedService.nextCart(this.total_product + 1);
        }
        // console.log(data)
      })
    }else if(action === ActionOrder.SUB){
      this.totalPrice -= Number(productPrice);
      formOrder.total_price = this.totalPrice;
      formOrder.action = ActionOrder.SUB;
      formOrder.product_id = productId;
      this.orderService.createOrder(formOrder).subscribe(data=>{
        this.disableDecreaseButton = false;
        this.disableButton = false;
        if(data.code === 200){
          this.sharedService.sharedMessage.subscribe(
            (message) => (this.total_product = message)
          );
          this.sharedService.nextCart(this.total_product - 1);
        }
        // console.log(data)
      })
    }

  }

}
