import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams, HttpResponse, } from '@angular/common/http'
import { catchError, map } from 'rxjs/operators';
import { Router } from '@angular/router';
import { VoucherBrand , ListVoucher , VoucherDetail} from './backend-api';
import { ApiService } from './api.service'
import { Observable, throwError } from 'rxjs';
import { IGetRowsParams } from 'ag-grid-community';
import { CheckNullOrUndefinedOrEmpty, RenderAddress } from '../utils/common-function';
import { branchAccount, branchStatus, accountInfor } from '../models/branch-account.model';
import { branchStore, storeDetail, storeInfor, 
 } from '../models/branch-store.model';
import * as _ from 'moment'
import { ManagerOfStore } from '../models/bk-point/store.model';
import { VoucherTable } from '../models/bk-point/voucher-bkpoint.model';
import { formatDate } from '@angular/common';
@Injectable({
    providedIn: 'root'
})
export class VoucherService
{

    constructor(
        private http: HttpClient,
        private router: Router,
        private api: ApiService
    ) { }

    createVoucherByBrand(formVoucher){
        return this.api.post(VoucherBrand, formVoucher);
    }

    getListVoucherByBrand(){
        return this.api.get(ListVoucher).pipe(
            map(data=>{
                if(data.code === 200){
                    // console.log(data.data)
                    return this.renderListVoucherData(data.data)
                }
            })
        )
    }

    getVoucherDetailByBrand(id){
        let param = new HttpParams();
        param = param.append('id', id);
        if(this.api.isEnable()){
            return this.http.get<any>(VoucherDetail  ,{headers : this.api.headers, params : param}).pipe(
                map(data=>{
                    if(data.code === 200){
                        return data.data
                    }
                })
            )
        }
    }

    updateVoucherDetail(id , formUpdate){
        let param = new HttpParams();
        param = param.append('id', id);
        if(this.api.isEnable()){
            return this.http.put<any>(VoucherDetail , formUpdate ,{headers : this.api.headers, params : param})
        }
    }

    renderListVoucherData(data){
        let listVoucher = [];
        if(!CheckNullOrUndefinedOrEmpty(data)){
            data.forEach(element => {
                let voucher = new VoucherTable();
                voucher.name = element.voucher_name;
                voucher.id = element.id;
                voucher.updatedAt = formatDate(element.updated_at,  'dd/MM/yyyy', "en-US");
                voucher.description = element.description;
                voucher.start_time = formatDate(element.start_time,  'dd/MM/yyyy', "en-US");
                voucher.end_time = formatDate(element.end_time,  'dd/MM/yyyy', "en-US");
                voucher.image = element.voucher_photo;
                listVoucher.push(voucher)
            });
        }
        return listVoucher;
    }

}
